import { getRemainingTimeToDateSeconds, getRemainingTimeToHourSeconds } from "../Utils";
import { UserController } from "../api/controllers/AuthController";
import { LashHourLeaderDto } from "../dtos/user.dto";
import DataManager from "../manager/DataManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class HourlyChallangeItem extends cc.Component {

    @property(cc.Sprite)
    public sprite_reward: cc.Sprite | null = null;

    @property(cc.Label)
    public label_reward: cc.Label | null = null;

    @property(cc.Label)
    public label_score: cc.Label | null = null;

    @property(cc.Label)
    public label_name: cc.Label | null = null;

    @property(cc.Label)
    public label_time: cc.Label | null = null;

    @property(cc.SpriteFrame)
    public shuffle_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public bomb_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public replace_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public coin_frame: cc.SpriteFrame = null;

    public id: number = null;
    public reward: string = null;
    public amount: number = null;

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    setTestData(){
        this.reward = "COIN";
        this.amount = 50;
        this.label_name.string = "@null";
        this.label_score.string = "0";

        this.sprite_reward.spriteFrame = this.getRewardSprite(this.reward);
        this.label_reward.string = `x${this.amount}`;
    }

    setFromCache(){
        if(DataManager.instance.lastHourLeader){
            this.set(DataManager.instance.lastHourLeader);
        }
        else{
            this.setTestData();
        }
    }

    fetchFromServer(){
        this.setFromCache();

        UserController.Instance?.getLastHourLeader().then((response) => {
            DataManager.instance.lastHourLeader = response;
            this.set(response);
        }).catch((error) => {
            console.error("Failed to fetch tasks:", error);
        });
    }

    set(dto: LashHourLeaderDto){
        const name = (dto.username && dto.username != "" && dto.username != null) ? `@${dto.username}` : `${dto.first_name}`;

        this.reward = "COIN";
        this.amount = 50;
        this.label_name.string = name;
        this.label_score.string = `${dto.points}`;

        this.sprite_reward.spriteFrame = this.getRewardSprite(this.reward);
        this.label_reward.string = `x${this.amount}`;
    }

    protected update(dt: number): void {
        const nextHour = this.getNextHourDate();
        this.label_time.string = getRemainingTimeToHourSeconds(nextHour);
    }


    getNextHourDate(): Date {
        const now = new Date();
        now.setMinutes(0, 0, 0); // Reset minutes, seconds, milliseconds
        now.setHours(now.getHours() + 1); // Move to next hour
        return now;
    }
    

    getRewardSprite(reward: string) : cc.SpriteFrame{
        switch(reward){
            case "BOMB":
                return this.bomb_frame;
            case "SHUFFLE":
                return this.shuffle_frame; 
            case "CHANGE_BLOCK":
                return this.replace_frame;
            case "COIN":
            case "STAR":
            case "STARS":

                return this.coin_frame;
            default:
                return null;
        }
    }

}