declare var CryptoJS: any;

import CryptoJS from  "../modules/crypto-js";
import * as config from '../../config.js';


export default class ConfigLoader {
    private static passphrase = "cGYBfTthjUrwMW/LtOoaDdkFKtnPuAm+VC28dDgNNm9EK0rfX3ExiUgtCcz6v2h3";

    static async loadConfig(): Promise<any> {
        return new Promise((resolve, reject) => {
            try {
                // Decrypt the data
                const decryptedBytes = CryptoJS.AES.decrypt(config.ENC_DATA, this.passphrase);
                const decryptedConfig = JSON.parse(decryptedBytes.toString(CryptoJS.enc.Utf8));
                resolve(decryptedConfig);
            } catch (e) {
                console.error("Decryption error:", e);
                reject(e);
            }
        });
    }
}

