const { ccclass, property } = cc._decorator;

@ccclass
export default class LeaderboardItem extends cc.Component {

    @property(cc.Label)
    public label_place: cc.Label | null = null;

    @property(cc.Label)
    public label_name: cc.Label | null = null;

    @property(cc.Label)
    public label_score: cc.Label | null = null;

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    init(place: number, name: string,  score: number){
        this.label_place.string = place.toString();
        this.label_name.string = name;
        this.label_score.string = score.toString();
    }
}
