import { UserController } from "../api/controllers/AuthController";
import { ENUM_AUDIO_CLIP, ENUM_GAME_STATUS, ENUM_UI_TYPE } from "../Enum";
import AudioManager from "../manager/AudioManager";
import DataManager from "../manager/DataManager";
import { StaticInstance } from "../StaticInstance";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import BaseLayer from "./Baselayer";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ShopLayer extends BaseLayer {

    btn_close: cc.Node = null;

    btn_shop_1: cc.Node = null;
    btn_shop_2: cc.Node = null;
    btn_shop_3: cc.Node = null;
    coins_amount_label: cc.Label = null;


    onLoad() {
        this.btn_close = cc.find('btn_close', this.node)
        this.btn_shop_1 = cc.find('layout/btn_buy_coins_1', this.node)
        this.btn_shop_2 = cc.find('layout/btn_buy_coins_2', this.node)
        this.btn_shop_3 = cc.find('layout/btn_buy_coins_3', this.node)
        this.coins_amount_label = cc.find('coin_balance/coins_amount', this.node).getComponent(cc.Label)

        this.btn_close.on('click', this.onBackClick, this)
        this.btn_shop_1.on('click', this.onBuyPack1Click, this)
        this.btn_shop_2.on('click', this.onBuyPack2Click, this)
        this.btn_shop_3.on('click', this.onBuyPack3Click, this)
    }

    onDestroy() {
        this.btn_close.off('click', this.onBackClick, this)
        this.btn_shop_1.off('click', this.onBuyPack1Click, this)
        this.btn_shop_2.off('click', this.onBuyPack2Click, this)
        this.btn_shop_3.off('click', this.onBuyPack3Click, this)
    }

    onEnable() { 
       this.fetchCoins();
    }

    onDisable() { }

    onBackClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false)
        //StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onBuyPack1Click(){
        this.buyCoinsForStars(5);
    }

    onBuyPack2Click(){
        this.buyCoinsForStars(100);
    }

    onBuyPack3Click(){
        this.buyCoinsForStars(500);
    }


    buyCoinsForStars(coins_amount: number){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        UserController.Instance?.postInvoiceStars(coins_amount).then((response) => {
            console.log(response.invoice_link);

            TelegramWebApp.Instance.openInvoice(response.invoice_link,  (status, url) =>{
                console.log("invoice callback");
                console.log(status);
                console.log(url);
                this.fetchCoins();
            });

        }).catch((error) => {
            console.error("Failed transaction:", error);
            this.fetchCoins();
        });
    }

    buyRemoveAds(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        UserController.Instance?.postInvoicePremium().then((response) => {
            console.log(response.invoice_link);

            TelegramWebApp.Instance.openInvoice(response.invoice_link,  (status, url) =>{
                console.log("invoice callback");
                console.log(status);
                console.log(url);
                this.fetchCoins();
            });

        }).catch((error) => {
            console.error("Failed transaction:", error);
            this.fetchCoins();
        });
    }

    fetchCoins(){
        UserController.Instance?.getUser().then((response) => {
            this.coins_amount_label.string = response.data.balance_stars.toString();
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });
    }

    public async fetchPremiumStatus() : Promise<boolean> {
        return (await UserController.Instance?.getUser()).data.is_premium;
    }
}
