declare var CryptoJS: any;
import CryptoJS from  "../modules/crypto-js";
import { ENUM_GAME_MODE, ENUM_GAME_STATUS } from '../Enum';
import { LashHourLeaderDto, UserAuthDto, UserDailyTasksDto } from "../dtos/user.dto";

const STORAGE_KEY = 'BLOCK_STARS'

export type ICallBack = {
    status: boolean,
    data: any,
    msg: string
}

export type IUser = {
    openid: string,
    nickname: string,
    gender: number,
    avatar: string
}

export default class DataManager {

    private static _instance: any = null

    static getInstance<T>(): T {
        if (this._instance === null) {
            this._instance = new this()
        }

        return this._instance
    }

    static get instance() {
        return this.getInstance<DataManager>()
    }
    
    config : any  = null
    userData : UserAuthDto = null
    userTasks : UserDailyTasksDto = null
    lastHourLeader : LashHourLeaderDto = null
    nextMonday: Date = null

    serverTimestamp: number = null;
    serverTimeOffsetMiliseconds: number = 0;


    // 游戏模式
    mode: ENUM_GAME_MODE = ENUM_GAME_MODE.SCORE
    // 游戏状态
    status: ENUM_GAME_STATUS = ENUM_GAME_STATUS.UNRUNING
    // 加载进度
    loadingRate: number = 0

    loadingMessage: string = null

    // 声音开启
    _isMusicOn: boolean = true
    _isSoundOn: boolean = true

    // 更多游戏
    games: any[] = [
        { title: '消灭星星', icon: 'xiao2d', appid: 'wxefd5a4ddd8e31b44', url: 'https://store.cocos.com/app/detail/4183' },
        { title: '实况足球杯', icon: 'football', appid: 'wx0c16e9d7f9e87dac', url: 'https://store.cocos.com/app/detail/4221' },
        { title: '爬了个爬', icon: 'stairway', appid: 'wx025bcf3a316bfa27', url: 'https://store.cocos.com/app/detail/4314' },
        { title: '咩了个咩3D', icon: 'xiao3d', appid: 'wx5841e5a26082b380', url: 'https://store.cocos.com/app/detail/4148' },
        { title: '经典泡泡龙', icon: 'bubble', appid: 'wxcc2f90afdf28ae3b', url: 'https://store.cocos.com/app/detail/4370' },
    ]
    // 关卡
    level: number = 1
    levelMax: number = 1
    // 分数
    score: number = 0
    scoreMax: number = 0

    // 历史数据
    historyData: { main: number[], ready: number[], score: number, bonusesUsed: number, isFreeBombUsed: boolean  } = null

    gameCreatedAtDate: Date = null
    language: string = "en"
    isDarkThemeOn: boolean = true

    get isMusicOn() {
        return this._isMusicOn
    }

    set isMusicOn(data: boolean) {
        this._isMusicOn = data
    }

    get isSoundOn() {
        return this._isSoundOn
    }

    set isSoundOn(data: boolean) {
        this._isSoundOn = data
    }

    init(config : any){
        this.config = config;
    }

    reset() {
        DataManager.instance.setGameStatusUnrunning();
    }

    // Generate a secure AES key from the passphrase
    generateSecureKey(passphrase: string): string {
        return CryptoJS.SHA256(passphrase).toString(CryptoJS.enc.Hex);
    }

    save() {
        const SECRET_KEY = this.generateSecureKey(this.config.SAVE_SECRET);

        const data = JSON.stringify({
            isSoundOn: this.isSoundOn,
            isMusicOn: this.isMusicOn,
            isDarkThemeOn: this.isDarkThemeOn,
            level: this.level,
            levelMax: this.levelMax,
            scoreMax: this.scoreMax,
            historyData: this.historyData,
            gameCreatedAtTimeStamp: this.gameCreatedAtDate ? this.gameCreatedAtDate.getTime() : null,
            language: this.language,
        });

        // Encrypt data with AES-256
        const encryptedData = CryptoJS.AES.encrypt(data, SECRET_KEY).toString();

        // Generate HMAC signature to verify data integrity
        const hmac = CryptoJS.HmacSHA256(encryptedData, SECRET_KEY).toString();

        // Store both encrypted data & HMAC signature
        const payload = JSON.stringify({ encryptedData, hmac });
        cc.sys.localStorage.setItem(STORAGE_KEY, payload);
    }

    restore() {
        const SECRET_KEY = this.generateSecureKey(this.config.SAVE_SECRET);
        const storedData = cc.sys.localStorage.getItem(STORAGE_KEY);

        if (!storedData) {
            this.resetDefaults();
            return;
        }

        try {
            // Parse stored JSON
            const { encryptedData, hmac } = JSON.parse(storedData);

            // Validate HMAC integrity
            const calculatedHmac = CryptoJS.HmacSHA256(encryptedData, SECRET_KEY).toString();
            if (calculatedHmac !== hmac) {
                console.error("Data integrity check failed!");
                this.resetDefaults();
                return;
            }

            // Decrypt data
            const bytes = CryptoJS.AES.decrypt(encryptedData, SECRET_KEY);
            const decryptedData = bytes.toString(CryptoJS.enc.Utf8);

            const data = JSON.parse(decryptedData);

            // Assign values safely
            this.isMusicOn = data?.isMusicOn ?? true;
            this.isSoundOn = data?.isSoundOn ?? true;
            this.isDarkThemeOn = data?.isDarkThemeOn ?? false;
            this.level = typeof data.level === "number" ? data.level : 1;
            this.levelMax = typeof data.levelMax === "number" ? data.levelMax : 1;
            this.scoreMax = typeof data.scoreMax === "number" ? data.scoreMax : 0;
            this.historyData = data?.historyData || null;
            this.gameCreatedAtDate = typeof data.gameCreatedAtTimeStamp === "number" ? new Date(data.gameCreatedAtTimeStamp) : null;
            this.language = data?.language || "en";
        } catch (error) {
            console.error("Failed to decrypt/parse data:", error);
            this.resetDefaults();
        }
    }

    resetDefaults() {
        this.isMusicOn = true;
        this.isSoundOn = true;
        this.isDarkThemeOn = false;
        this.level = 1;
        this.levelMax = 1;
        this.scoreMax = 0;
        this.historyData = null;
        this.gameCreatedAtDate = null;
        this.language = "en";
    }

    setGameStatusRunning(){
        try{
            document.getElementById("ads-banner-1").setAttribute("style","position:fixed;bottom:0%;width:100%;display:flex;justify-content:center;");
        }
        catch{

        }
        DataManager.instance.status = ENUM_GAME_STATUS.RUNING
    }

    setGameStatusUnrunning(){
        try{
        document.getElementById("ads-banner-1").setAttribute("style","position:fixed;bottom:0%;width:100%;display:none;justify-content:center;");
        }
        catch{
            
        }
        DataManager.instance.status = ENUM_GAME_STATUS.UNRUNING
    }
}
