//import {i18n, LocalizedLabel} from "../../../packages/i18n";

import ALocaleLabel from "../localization/ALocaleLabel"
import DataManager from "./DataManager"

export default class LocalizationManager {

    private static _instance: any = null

    static getInstance<T>(): T {
        if (this._instance === null) {
            this._instance = new this()
            this._instance.init()
        }

        return this._instance
    }

    static get instance() {
        return this.getInstance<LocalizationManager>()
    }

    init(){
        
    }

    changeLanguage(langCode) {
        cc.log(`Language changed to: ${langCode}`);
        DataManager.instance.language = langCode;
        DataManager.instance.save();
        this.refreshUI();
    }

    getLocalizedString(key : string) : string{
        var localizedString = jsonLocalization[key];

        if(!localizedString){
            return "null";
        }

        switch (DataManager.instance.language){
            case "en":
                return localizedString.en;
            case "ua":
            case "uk":
                return localizedString.ua;
            case "de":
                return localizedString.de;
            case "es":
                return localizedString.es;
            case "ru":
                return localizedString.ru;
            case "cs":
                return localizedString.cs;
            case "sk":
                return localizedString.sk;
            case "pl":
                return localizedString.pl;
            default:
                return localizedString.en;
        }
    }

    refreshUI() {

        console.log("refreshUI " +   DataManager.instance.language );

        // Refresh any text elements that need to be updated
        let labels = cc.find("Canvas").getComponentsInChildren(ALocaleLabel);
        labels.forEach(label => {
            label.fetchRender();
        });
    }
};

