    //games weekly

    "games_weekly_text": {
        "en": "Leaderboard \"Best Result\" – Show your best result of the week and become a star of the game!\n\nAdditionally, complete the task of playing 5 games with a score of 500+ and take part in the draw for 1 of 3 spots, each receiving 5% of the stars from the pool.",
        "ua": "Лідерборд \"Найкращий результат\" — покажи свій найкращий результат за тиждень та стань зіркою гри!\n\nДодатково виконай завдання на 5 ігор з результатом 500+ та візьми участь в розіграші 1 з 3 місць по 5% зірок із пулу.",
        "de": "Bestenliste \"Bestes Ergebnis\" – Zeige dein bestes Ergebnis der Woche und werde zum Star des Spiels!\n\nZusätzlich absolviere 5 Spiele mit einer Punktzahl von 500+ und nimm an der Verlosung von 1 von 3 Plätzen teil, um jeweils 5 % der Sterne aus dem Pool zu gewinnen.",
        "es": "Clasificación \"Mejor resultado\" – ¡Muestra tu mejor resultado de la semana y conviértete en la estrella del juego!\n\nAdemás, completa la tarea de jugar 5 partidas con una puntuación de 500+ y participa en el sorteo de 1 de 3 plazas para ganar el 5% de las estrellas del fondo.",
        "ru": "Лидерборд \"Лучший результат\" – покажи свой лучший результат за неделю и стань звездой игры!\n\nДополнительно выполни задание: сыграй 5 игр с результатом 500+ и прими участие в розыгрыше 1 из 3 мест, каждая из которых получает 5% звёзд из пула.",
        "cs": "Žebříček \"Nejlepší výsledek\" – Ukážte svůj nejlepší výsledek týdne a staňte se hvězdou hry!\n\nDále splňte úkol 5 her s výsledkem 500+ a zúčastněte se losování o 1 z 3 míst, přičemž každé z nich získá 5 % hvězd z poolu.",
        "sk": "Žebříček \"Najlepší výsledok\" – Ukážte svoj najlepší výsledok týždňa a staňte sa hviezdou hry!\n\nOkrem toho splňte úlohu 5 hier s výsledkom 500+ a zapojte sa do žrebovania o 1 z 3 miest, pričom každé z nich získa 5 % hviezd z poolu.",
        "pl": "Tablica wyników \"Najlepszy wynik\" – Pokaż swój najlepszy wynik tygodnia i zostań gwiazdą gry!\n\nDodatkowo wykonaj zadanie polegające na rozegraniu 5 gier z wynikiem 500+ i weź udział w losowaniu 1 z 3 miejsc, z których każde otrzyma 5% gwiazdek z puli."
    },
    
    "games_weekly_text_2": {
        "en": "Leaderboard \"Total Score\" – Prove that you're a consistently great player by scoring 250+ points per game and achieving the highest weekly total!\n\nAdditionally, complete the task of reaching a total of 5000 points and take part in the draw for 1 of 3 spots, each receiving 5% of the stars from the pool.",
        "ua": "Лідерборд \"Сумарний результат\" — доведи, що ти стабільно крутий гравець, набираючи 250+ балів за гру та отримай найвищу суму тижневих балів!\n\nДодатково виконай завдання на загальну суму в 5000 балів та візьми участь в розіграші 1 з 3 місць по 5% зірок із пулу.",
        "de": "Bestenliste \"Gesamtpunktzahl\" – Beweise, dass du ein konstant großartiger Spieler bist, indem du 250+ Punkte pro Spiel erzielst und die höchste wöchentliche Gesamtpunktzahl erreichst!\n\nZusätzlich absolviere eine Gesamtpunktzahl von 5000 und nimm an der Verlosung von 1 von 3 Plätzen teil, um jeweils 5 % der Sterne aus dem Pool zu gewinnen.",
        "es": "Clasificación \"Puntuación total\" – ¡Demuestra que eres un jugador constante alcanzando 250+ puntos por partida y obteniendo la puntuación total más alta de la semana!\n\nAdemás, completa la tarea de alcanzar un total de 5000 puntos y participa en el sorteo de 1 de 3 plazas para ganar el 5% de las estrellas del fondo.",
        "ru": "Лидерборд \"Суммарный результат\" – докажи, что ты стабильно крутой игрок, набирая 250+ очков за игру и получая наивысшую сумму очков за неделю!\n\nДополнительно выполни задание на общую сумму 5000 очков и прими участие в розыгрыше 1 из 3 мест, каждая из которых получает 5% звёзд из пула.",
        "cs": "Žebříček \"Celkový skóre\" – Dokaž, že jsi konzistentně skvělý hráč tím, že získáš 250+ bodů za hru a dosáhneš nejvyšší týdenní celkový výsledek!\n\nDále splňte úkol dosažení celkového skóre 5000 bodů a zúčastněte se losování o 1 z 3 míst, přičemž každé z nich získá 5 % hvězd z poolu.",
        "sk": "Žebříček \"Celkové skóre\" – Dokáž, že si konzistentne skvelý hráč tým, že získaš 250+ bodov za hru a dosiahneš najvyšší týždenný celkový výsledok!\n\nOkrem toho splňte úlohu dosiahnuť celkový počet 5000 bodov a zapojte sa do žrebovania o 1 z 3 miest, pričom každé z nich získa 5 % hviezd z poolu.",
        "pl": "Tablica wyników \"Łączny wynik\" – Udowodnij, że jesteś konsekwentnie świetnym graczem, zdobywając 250+ punktów na grę i osiągając najwyższy tygodniowy wynik!\n\nDodatkowo wykonaj zadanie polegające na uzyskaniu łącznie 5000 punktów i weź udział w losowaniu 1 z 3 miejsc, z których każde otrzyma 5% gwiazdek z puli."
    },
    
