const { ccclass, property } = cc._decorator;

import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_GAME_STATUS, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from "../StaticInstance";
import { generateGameToken, getServerTimestampNow, isOnline } from "../Utils";
import { UserController } from "../api/controllers/AuthController";
import Level from "../game/Level";
import AudioManager from "./AudioManager";
import DataManager from "./DataManager";
import PoolManager from "./PoolManager";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import ConfigLoader from "../utils/ConfigLoader";

@ccclass
export default class GameManager extends cc.Component {

    stage: cc.Node = null;

    onLoad() {
        StaticInstance.setGameManager(this);
        this.stage = cc.find('Stage', this.node);
    }

    onDestroy() { }

    // Почати гру
    onGameStart() {
        DataManager.instance.reset();
        this.initGame();
    }

    // Завершити гру
    async onGameOver(ui: ENUM_UI_TYPE) {
        DataManager.instance.setGameStatusUnrunning();
        if (ui == ENUM_UI_TYPE.LOSE) {
            if (DataManager.instance.mode == ENUM_GAME_MODE.SCORE) {
                const score = DataManager.instance.score;
                const timestamp = getServerTimestampNow();

                var userId;
                try{
                    userId = TelegramWebApp.Instance.getTelegramUser().id
                }
                catch{
                    await UserController.Instance?.getUser().then((response) => {
                        userId = response.data.id;
                    }).catch((error) => {
                        console.error("Failed to fetch user data:", error);
                    });
                }

                const config = await ConfigLoader.loadConfig();

                generateGameToken(userId, timestamp, config.GAME_SECRET_KEY, score).then((res)=>{
                    const gameToken = res;
                    UserController.Instance?.postGameFinish(score, gameToken, timestamp).then((response) => {
                        //console.log("game ended token", response)
                    }).catch((error) => {
                        console.error("Failed to end game:", error);
                        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR)
                        return;
                    });

                }).catch((error) => {
                    console.error("Failed to end game:", error);
                    StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR)
                    return;
                });
                
                DataManager.instance.gameCreatedAtDate = null;
                DataManager.instance.historyData = null;
                DataManager.instance.save();
            }
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR, false)

            AudioManager.instance.playSound(ENUM_AUDIO_CLIP.LOSE);
            this.scheduleOnce(() => {
                StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LOSE);
            }, 0.3);
        }
    }

    // Ініціалізація гри
    async initGame() {

        if(!DataManager.instance.historyData){
            const timestamp = getServerTimestampNow();
            var userId;
            try{
                userId = TelegramWebApp.Instance.getTelegramUser().id
            }
            catch{
                await UserController.Instance?.getUser().then((response) => {
                    userId = response.data.id;
                }).catch((error) => {
                    console.error("Failed to fetch user data:", error);
                });
            }

            const config = await ConfigLoader.loadConfig();

            try {
                console.log(config.GAME_SECRET_KEY)

                const gameToken = await generateGameToken(userId, timestamp, config.GAME_SECRET_KEY);
            
                try {
                    await UserController.Instance?.postGameStart(gameToken, timestamp);
                    console.log("Game started successfully");
                    DataManager.instance.gameCreatedAtDate = new Date();
                } catch (error) {
                    console.error("Failed to start game:", error);
                    StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR);
                    StaticInstance.fadeManager.fadeOut();
                    return;
                }
            } catch (error) {
                console.error("Failed to generate game token:", error);
                StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR);
                StaticInstance.fadeManager.fadeOut();
                return;
            }
        }

        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SERVER_HTTP_ERROR, false)
        DataManager.instance.setGameStatusUnrunning();
        this.stage.removeAllChildren();
        const levelScore = PoolManager.instance.getNode('LevelScore', this.stage);
        levelScore.getComponent(Level).init();
        DataManager.instance.setGameStatusRunning();
        await StaticInstance.fadeManager.fadeOut();
    }
}
