import { UserController } from "../api/controllers/AuthController";
import { AdsGramController } from "./ads-gram/AdsGram";
import { MonetagController } from "./monetag/Monetag";
import { Onclicka } from "./onclicka/Onclicka";
import { Richads } from "./richads/Richads";

const { ccclass, property } = cc._decorator;

@ccclass('AdsManager')
export class AdsManager {
    private static _instance: AdsManager;
    private constructor() {}

    public static get Instance(): AdsManager {
        if (!AdsManager._instance) {
            AdsManager._instance = new AdsManager();
        }
        return AdsManager._instance;
    }

    private monetag: MonetagController;
    private adsGram: AdsGramController;
    private onclicka: Onclicka;
    private richads: Richads;

    public async init() {
        console.log("INITIALIZING ADS...");

        this.monetag = MonetagController.Instance;
        this.adsGram = AdsGramController.Instance;
        this.onclicka = Onclicka.Instance;
        this.richads = Richads.Instance;

        this.monetag.init().then(res => {
            console.log("MonetagController init succes: ", res.success);
        })

        this.onclicka.init()

        try{
            await this.adsGram.init()
            AdsGramController.Instance.initAdsContorllers();
            console.log("adsgram init succes: ", true);
        }
        catch(error) {
            console.warn(`Failed to init adsgram:`, error);
        }
    }


    public async showAd_Rewarded_Intestitial(): Promise<boolean> {
        for (const provider of this.rewarded_providers) {
            const success = await this.tryShowAdFromProvider(provider);
            if (success) {
                await this.patchAd();
                return success;
            }
        }
        console.warn('No ad was successfully shown from any provider.');
    }

    public async showAd_InApp_Intestitial(): Promise<boolean> {
        for (const provider of this.inApp_providers) {
            const success = await this.tryShowAdFromProvider(provider);
            if (success) {
                await this.patchAd();
                return success;
            }
        }
        console.warn('No ad was successfully shown from any provider.');
    }

    private async tryShowAdFromProvider(provider: AdProvider): Promise<boolean> {
        try {
            if (provider.requiresPreload) {
                await provider.preload?.();
                console.log(`Ad successfully preloaded from: ${provider.name}`);
            }
            
            await provider.show();
            console.log(`Ad successfully shown from: ${provider.name}`);
            return true;
        } catch (error) {
            console.warn(`Failed to show ad from: ${provider.name}`, error);
            return false;
        }
    }

    private patchAd() {
        console.log('You have seen ad ad!');
        try { UserController.Instance?.patchAd(); }
        catch { e => console.log(e) }
    }


    private rewarded_providers: AdProvider[] = [
        {
            name: 'monetag',
            preload: () => this.monetag.preloadRewarded_Interstitial_Timeout(),
            show: () => this.monetag.showRewarded_Interstitial(),
            requiresPreload: true,
        },
        {
            name: 'richads',
            show: () => this.richads.showAd_Rewarded(),
            requiresPreload: false,
        },
        {
            name: 'onclicka',
            show: () => this.onclicka.showAd_Rewarded(),
            requiresPreload: false,
        },
        {
            name: 'adsGram',
            show: () => this.adsGram.showAd_Rewarded(),
            requiresPreload: false,
        },
    ];

    private inApp_providers: AdProvider[] = [
        {
            name: 'monetag',
            preload: () => this.monetag.preloadInApp_Interstitial_Timeout(),
            show: () => this.monetag.showInApp_Interstitial(),
            requiresPreload: true,
        },
        {
            name: 'onclicka',
            show: () => this.onclicka.showAd_Rewarded(),
            requiresPreload: false,
        },
        {
            name: 'adsGram',
            show: () => this.adsGram.showAd_Intersitital(),
            requiresPreload: false,
        },
    ];
}

interface AdProvider {
    name: string;
    preload?: () => Promise<any>;
    show: () => Promise<any>;
    requiresPreload: boolean;
}


