// Created by carolsail

import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import { UserController } from "../api/controllers/AuthController";
import { getRemainingTimeToDateMinutes, getRemainingTimeToDateSeconds, getServerTimeNow, setDesignResolution, starsToDollars} from "../Utils";

const { ccclass, property } = cc._decorator;

@ccclass
export default class MenuLayer extends BaseLayer {

    btnStart: cc.Node = null

    //bottomPanel
    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null
    btnOutput: cc.Node = null

    btnShop: cc.Node = null
    labelCoinsAmount: cc.Label = null;

    btnGiveaway: cc.Node = null

    giveaway: cc.Node = null
    giveawayPanel: cc.Node = null
    btnCloseGiveaway: cc.Node = null

    labelGiveawayStarsAmount: cc.Label = null;
    labelGiveawayStarsDollarsAmount: cc.Label = null;
    labelGiveawayTimeLeft: cc.Label = null;

    labelGiveawayPanelStarsAmount: cc.Label = null;
    labelGiveawayPanelTimeLeft: cc.Label = null;

    //games weekly
    btnGamesWeekly: cc.Node = null
    panelGamesWeekly: cc.Node = null
    btnCloseGamesWeekly: cc.Node = null
    btnSeeLeaderboardWeekly: cc.Node = null

    labelGamesWeeklyFill: cc.Sprite = null;
    labelGamesWeeklyAmount: cc.Label = null;
    
    labelGamesWeeklyPanelFill: cc.Sprite = null;
    labelGamesWeeklyPanelAmount: cc.Label = null;
    labelGamesWeeklyPanelTimeLeft: cc.Label = null;


    //games total weekly
    btnGamesTotalWeekly: cc.Node = null
    panelGamesTotalWeekly: cc.Node = null
    btnCloseGamesTotalWeekly: cc.Node = null
    btnSeeLeaderboardTotalWeekly: cc.Node = null

    labelGamesTotalWeeklyFill: cc.Sprite = null;
    labelGamesTotalWeeklyAmount: cc.Label = null;

    labelGamesTotalWeeklyPanelFill: cc.Sprite = null;
    labelGamesTotalWeeklyPanelAmount: cc.Label = null;
    labelGamesTotalWeeklyPanelTimeLeft: cc.Label = null;

    onLoad() {
        this.btnStart = cc.find('main/btn_start', this.node)

        this.btnShop = cc.find('main/btn_coin_balance', this.node)
        this.labelCoinsAmount = cc.find('main/btn_coin_balance/coins_amount', this.node).getComponent(cc.Label)

        this.btnMain = cc.find('tabs_panel/btn_main', this.node)
        this.btnLeaderboard = cc.find('tabs_panel/btn_leader', this.node)
        this.btnTasks = cc.find('tabs_panel/btn_tasks', this.node)
        this.btnProfile = cc.find('tabs_panel/btn_profile', this.node)
        this.btnOutput = cc.find('tabs_panel/btn_out', this.node)
        

        //giveaway
        this.btnGiveaway = cc.find('main/btn_giveaway', this.node)
        this.giveaway = cc.find('giveaway', this.node)
        this.giveawayPanel = cc.find('panel', this.giveaway)
        this.btnCloseGiveaway = cc.find('btn_close', this.giveawayPanel)

        this.labelGiveawayStarsAmount = cc.find('main/btn_giveaway/layout/amount_collected', this.node).getComponent(cc.Label)
        this.labelGiveawayStarsDollarsAmount = cc.find('main/btn_giveaway/layout/amount_dollars', this.node).getComponent(cc.Label)

        this.labelGiveawayTimeLeft = cc.find('main/btn_giveaway/time_left', this.node).getComponent(cc.Label)

        this.labelGiveawayPanelStarsAmount = cc.find('top/pool_amount', this.giveawayPanel).getComponent(cc.Label)
        this.labelGiveawayPanelTimeLeft = cc.find('bottom/time_text', this.giveawayPanel).getComponent(cc.Label)


        //games weekly
        this.btnGamesWeekly = cc.find('main/btn_games_weekly', this.node)
        this.labelGamesWeeklyAmount = cc.find('amount', this.btnGamesWeekly).getComponent(cc.Label)
        this.labelGamesWeeklyFill = cc.find('fill_bar/fill', this.btnGamesWeekly).getComponent(cc.Sprite)

        this.panelGamesWeekly = cc.find('games_weekly', this.node)
        this.btnCloseGamesWeekly = cc.find('panel/btn_close', this.panelGamesWeekly)
        this.btnSeeLeaderboardWeekly = cc.find('panel/btn_leaderboard', this.panelGamesWeekly)

        this.labelGamesWeeklyPanelFill = cc.find('panel/games_weekly/fill_bar/fill', this.panelGamesWeekly).getComponent(cc.Sprite)
        this.labelGamesWeeklyPanelAmount = cc.find('panel/games_weekly/amount', this.panelGamesWeekly).getComponent(cc.Label)
        this.labelGamesWeeklyPanelTimeLeft = cc.find('panel/bottom/time_text', this.panelGamesWeekly).getComponent(cc.Label)

        this.setGamesWeeklyAmount(0,5);

        //games weekly total
        this.btnGamesTotalWeekly = cc.find('main/btn_games_weekly_2', this.node)
        this.labelGamesTotalWeeklyAmount = cc.find('amount', this.btnGamesTotalWeekly).getComponent(cc.Label)
        this.labelGamesTotalWeeklyFill = cc.find('fill_bar/fill', this.btnGamesTotalWeekly).getComponent(cc.Sprite)

        this.panelGamesTotalWeekly = cc.find('games_weekly_2', this.node)
        this.btnCloseGamesTotalWeekly = cc.find('panel/btn_close', this.panelGamesTotalWeekly)
        this.btnSeeLeaderboardTotalWeekly = cc.find('panel/btn_leaderboard', this.panelGamesTotalWeekly)

        this.labelGamesTotalWeeklyPanelFill = cc.find('panel/games_weekly/fill_bar/fill', this.panelGamesTotalWeekly).getComponent(cc.Sprite)
        this.labelGamesTotalWeeklyPanelAmount = cc.find('panel/games_weekly/amount', this.panelGamesTotalWeekly).getComponent(cc.Label)
        this.labelGamesTotalWeeklyPanelTimeLeft = cc.find('panel/bottom/time_text', this.panelGamesTotalWeekly).getComponent(cc.Label)

        this.setGamesTotalWeeklyAmount(0,5000);



        this.btnStart.on('click', this.onStartClick, this)

        this.btnMain.on('click', this.onMainClick, this)
        this.btnLeaderboard.on('click', this.onLeaderboardClick, this)
        this.btnTasks.on('click', this.onTasksClick, this)
        this.btnProfile.on('click', this.onProfileClick, this)
        this.btnOutput.on('click', this.onOutputClick, this)
        this.btnShop.on('click', this.onShopClick, this)

        this.btnGiveaway.on('click', this.onGiveawayClick, this)
        this.giveawayPanel.on('click', this.onCloseGiveawayClick, this)
        this.btnCloseGiveaway.on('click', this.onCloseGiveawayClick, this)

        this.btnGamesWeekly.on('click', this.onGamesWeeklyClick, this)
        this.btnCloseGamesWeekly.on('click', this.onCloseGamesWeeklyClick, this)
        this.btnSeeLeaderboardWeekly.on('click', this.onGamesWeeklyLeaderboardClick, this)

        this.btnGamesTotalWeekly.on('click', this.onGamesTotalWeeklyClick, this)
        this.btnCloseGamesTotalWeekly.on('click', this.onCloseGamesTotalWeeklyClick, this)
        this.btnSeeLeaderboardTotalWeekly.on('click', this.onGamesTotalWeeklyLeaderboardClick, this)

    }

    setGamesWeeklyAmount(played : number, target : number){
        const fillRange = played / target;
        this.labelGamesWeeklyPanelAmount.string = this.labelGamesWeeklyAmount.string = `${played}/${target}`
        this.labelGamesWeeklyPanelFill.fillRange = this.labelGamesWeeklyFill.fillRange = fillRange;
    }

    setGamesTotalWeeklyAmount(played : number, target : number){
        const fillRange = played / target;
        this.labelGamesTotalWeeklyPanelAmount.string = this.labelGamesTotalWeeklyAmount.string = `${played}/${target}`
        this.labelGamesTotalWeeklyPanelFill.fillRange = this.labelGamesTotalWeeklyFill.fillRange = fillRange;
    }

    onDestroy() {
        this.btnStart.off('click', this.onStartClick, this)

        this.btnMain.off('click', this.onMainClick, this)
        this.btnLeaderboard.off('click', this.onLeaderboardClick, this)
        this.btnTasks.off('click', this.onTasksClick, this)
        this.btnProfile.off('click', this.onProfileClick, this)
        this.btnOutput.off('click', this.onOutputClick, this)
        this.btnShop.off('click', this.onShopClick, this)

        this.btnGiveaway.off('click', this.onGiveawayClick, this)
        this.giveawayPanel.off('click', this.onCloseGiveawayClick, this)
        this.btnCloseGiveaway.off('click', this.onCloseGiveawayClick, this)
    }

    async onEnable() {
        this.giveaway.active = false;

        if(DataManager.instance.userData){
            this.labelCoinsAmount.string = DataManager.instance.userData.balance_stars.toString();
        }

        UserController.Instance?.getUser().then((response) => {
            DataManager.instance.userData = response.data;
            this.labelCoinsAmount.string = response.data.balance_stars.toString();
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });


        while (DataManager.instance.userData == null) {
            await new Promise(resolve => setTimeout(resolve, 10)); // Wait 10ms before checking again
        }

        UserController.Instance?.getCurrentPrizePool().then((response) => {

            var secondsToNextMonday = response.data.seconds_to_next_monday;
            const nextMonday = getServerTimeNow(); // Get the current date and time
            nextMonday.setSeconds(nextMonday.getSeconds() + secondsToNextMonday);
            DataManager.instance.nextMonday = nextMonday;

            this.labelGiveawayPanelStarsAmount.string = 
            this.labelGiveawayStarsAmount.string = response.data.total_winning_amount.toFixed(2);;
            this.labelGiveawayStarsDollarsAmount.string = `= $${starsToDollars(response.data.total_winning_amount).toFixed(2)}`

        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });

        UserController.Instance.getGames500_count().then(response => {
            console.log(response);
            this.setGamesWeeklyAmount(response.count, 5);
        });

        UserController.Instance.getGames250_count().then(response => {
            console.log(response);
            this.setGamesTotalWeeklyAmount(response.sum_points, 5000);
        });


        UserController.Instance.getGamesMaxPoints().then(response => {
            console.log(response);
            DataManager.instance.scoreMax = typeof response.points === "number" ? response.points : 0;
        });
    }

    onDisable() { }

    protected update(dt: number): void {
        if(DataManager.instance.nextMonday){
            this.labelGiveawayTimeLeft.string = getRemainingTimeToDateMinutes(DataManager.instance.nextMonday);

            this.labelGamesTotalWeeklyPanelTimeLeft.string = 
            this.labelGamesWeeklyPanelTimeLeft.string = 
            this.labelGiveawayPanelTimeLeft.string = getRemainingTimeToDateSeconds(DataManager.instance.nextMonday);
        } 
    }

    async onStartClick() {
        setDesignResolution();
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        DataManager.instance.mode = ENUM_GAME_MODE.SCORE
        await StaticInstance.fadeManager.fadeIn()
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MAIN)
        DataManager.instance.score = 0
        StaticInstance.gameManager.onGameStart()
    }

    onMainClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
        setDesignResolution();
    }

    onLeaderboardClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false) 
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
        setDesignResolution();
    }

    onTasksClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
        setDesignResolution();
    }

    onProfileClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE)
        setDesignResolution();
    }

    onOutputClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.OUTPUT)
        setDesignResolution();
    }

    onShopClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        //StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP)
        setDesignResolution();
    }

    onGiveawayClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.giveaway.active = true;
    }
    
    onCloseGiveawayClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.giveaway.active = false;
    }

    onGamesWeeklyClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesWeekly.active = true;
    }

    onCloseGamesWeeklyClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesWeekly.active = false;
    }

    onGamesTotalWeeklyClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesTotalWeekly.active = true;
    }

    onCloseGamesTotalWeeklyClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesTotalWeekly.active = false;
    }

    onGamesWeeklyLeaderboardClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesWeekly.active = false;
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false) 
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onGamesTotalWeeklyLeaderboardClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.panelGamesTotalWeekly.active = false;
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false) 
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED)
    }
}
