            this.btn_buy_skill_1_confirm.off('click', this.buy_skill_1_shuffle_confirm_clicked, this)
            this.btn_buy_skill_2_confirm.off('click', this.buy_skill_2_bomb_confirm_clicked, this)
            this.btn_buy_skill_3_confirm.off('click', this.buy_skill_3_select_confirm_clicked, this)

            this.btn_buy_skill_1_decline.off('click', this.closeBuySkill_1, this)
            this.btn_buy_skill_2_decline.off('click', this.closeBuySkill_2, this)
            this.btn_buy_skill_3_decline.off('click', this.closeBuySkill_3, this)

            //skill - use
            this.btn_use_skill_1_confirm.off('click', this.use_skill_1_shuffle_confirm_clicked, this)
            this.btn_use_skill_2_confirm.off('click', this.use_skill_2_bomb_confirm_clicked, this)
