import { UserTransactionsType } from "../../constants/user-transactions.const";
import { FinishGameDto, GameFinishResponceDto, GameStartResponceDto, StartGameDto } from "../../dtos/game.dto";
import { CreateStarsInvoiceDto } from "../../dtos/invoice.dto";
import { LeaderboardDto } from "../../dtos/leaderboard.dto";
import { CurrentPrizePoolDto, LastDrawnPrizePoolDto } from "../../dtos/prize-pool.dto";
import { CreateUserTransactionDto } from "../../dtos/user-transaction.dto";
import { GetServerTimeDto, GetUserAuthDto, GetUserDailyTasksDto, LashHourLeaderDto, PatchBonusesDto, PatchTaskDto } from "../../dtos/user.dto";
import { WinnersDto } from "../../dtos/winners.dto";
import { TelegramWebApp } from "../../telegram/TelegramMiniApp";
import {ApiClient} from "../ApiClient";

export class UserController {
    constructor(private readonly apiClient: ApiClient) {}

    public static Instance: UserController = null

    //time
    public async getTime(): Promise<GetServerTimeDto> {
        return this.apiClient.get("time");
    }

    //user
    public async getUser(): Promise<GetUserAuthDto> {
        return this.apiClient.get("user");
    }

    //channel subscription

    public async getChannelMembership(): Promise<ChannelMembershipDto> {
        return this.apiClient.get("telegram/chat-member/"+TelegramWebApp.Instance.getTelegramUser().id);
    }

    //ads
    public async patchAd(): Promise<any> {
        const data = {
            number_of_ads: 1
        };
        return this.apiClient.patch("user/ads", data);
    }

    //tasks
    public async getUserDailyTasks(): Promise<GetUserDailyTasksDto> {
        return this.apiClient.get("user/daily-tasks");
    }

    public async patchDailyTasksIncrement(data: PatchTaskDto) : Promise<any>{
        return this.apiClient.patch("user/daily-tasks/increment", data);
    }

    //tasks - hour leader
    public async getLastHourLeader(): Promise<LashHourLeaderDto> {
        return this.apiClient.get("games/leader/last-hour");
    }

    //todo decrement bonuses from user.data than patch LEVEL. updateBonuses

    public async patchBonuses(data: PatchBonusesDto): Promise<any> {
        return this.apiClient.patch("user/bonuses", data);
    }

    //transactions

    public async getTransactions(): Promise<any> {
        return this.apiClient.get("transactions");
    }

    public async getTransactionById(id : number): Promise<any> {
        return this.apiClient.get("transactions/" + id);
    }

    //creates new transaction
    public async postCoinsTransaction(coins_amount: number, comission_amount: number): Promise<any> {
        const data: CreateUserTransactionDto = {
            amount_stars: coins_amount,
            commission: comission_amount,
            type: "ADD_COINS",
        };
        return this.apiClient.post("transactions", data);
    }

    public async postBonusTransaction(transactionType : keyof typeof UserTransactionsType): Promise<any> {
        const data: CreateUserTransactionDto = {
            type: transactionType,
        };
        return this.apiClient.post("transactions", data);
    }

    //leaderboard
    public async getLeaderboard(): Promise<LeaderboardDto> {
        return this.apiClient.get("games/leaderboard");
    }

    //leaderboard - combined points
    public async getSumPointsLeaderboard(): Promise<LeaderboardDto> {
        return this.apiClient.get("games/leaderboard/sum-points?limit=100");
    }

    //winners
    public async getLastWinners(): Promise<WinnersDto> {
        return this.apiClient.get("/winners/last-week?type=BEST");  
    }

   //winners - combined points
   public async getSumPointsLastWinners(): Promise<WinnersDto> {
    return this.apiClient.get("winners/last-week?type=TOTAL");
}

    //prize pool
    public async getCurrentPrizePool(): Promise<CurrentPrizePoolDto> {
        return this.apiClient.get("prize-pool/current");
    }

    public async getLastWeekPrizePool(): Promise<LastDrawnPrizePoolDto> {
        return this.apiClient.get("prize-pool/last-drawn");
    }
    

    //games

    public async getGames(): Promise<any> {
        return this.apiClient.get("games");
    }

    public async getGamesMaxPoints(): Promise<any> {
        return this.apiClient.get("games/max-points");
    }

    public async getGames500_count(): Promise<any> {
        return this.apiClient.get("games/count");
    }

    public async getGames250_count(): Promise<any> {
        return this.apiClient.get("games/sum-points");
    }

    public async postGameStart(gameToken: string, timeStamp: number): Promise<GameStartResponceDto> {
        const data: StartGameDto = {
            game_token: gameToken,
            timestamp: timeStamp.toString()
        };
        return this.apiClient.post("games/start", data);
    }

    public async postGameFinish(scorePoints: number, gameToken: string, timeStamp: number): Promise<GameFinishResponceDto> {
        const data: FinishGameDto = {
            points: scorePoints,
            game_token: gameToken, 
            timestamp: timeStamp.toString()
        };
        return this.apiClient.post("games/finish", data);
    }


    //Invoices

    public async postInvoiceStars(buyAmount: number): Promise<any> {
        const data: CreateStarsInvoiceDto = {
            amount: buyAmount,
        };
        return this.apiClient.post("invoices/stars", data);
    }

    public async postInvoicePremium(): Promise<any> {
        return this.apiClient.post("invoices/premium");
    }
}