export class CreateUserDto {
  public id!: number;
  public allows_write_to_pm!: boolean;
  public first_name!: string;
  public last_name!: string;
  public username!: string;
  public language_code!: string;
  public photo_url!: string;
}

export class UpdateUserDto {
  public allows_write_to_pm?: boolean;
  public first_name?: string;
  public last_name?: string;
  public username?: string;
  public language_code?: string;
  public photo_url?: string;
}

export class UserAuthDto{
  id: number;
  allows_write_to_pm: boolean;
  first_name: string;
  last_name: string;
  username: string;
  language_code: string;
  photo_url: string;
  balance_stars: number;
  is_premium: boolean;
  first_login: boolean;
  number_of_ads: number;
  bonuses?: BonusesDto | null;
  completed_tasks: number;
  weekly_streak: number;
}

export class GetUserAuthDto{
  data: UserAuthDto;
  message: string;
}

export class GetUserDailyTasksDto{
  data: UserDailyTasksDto;
  message: string;
}

export class UserDailyTasksDto{
  created_at: string;
  tasks: TaskDto[] 
}

export class TaskDto {
  id: number;
  sub_tasks: SubTaskDto[];
  reward: string;
  amount: number;
}

export class SubTaskDto {
  type: string;
  amount: number;
  target_amount: number;
}

export class PatchTaskDto {
  task: PatchTaskDataDto;
  token: string;
  timestamp: number;
}

export class PatchTaskDataDto {
  id: number;
  type: string;
  amount: number;
}

export class PatchBonusesDto {
  bonuses: BonusesDto;
  token: string;
  timestamp: number;
}

export class BonusesDto {
  bomb_amount: number;
  shuffle_amount: number;
  change_block_amount: number;
}

export class LashHourLeaderDto{
  public position: number;
  public user_id: string;
  public first_name?: string;
  public last_name?: string;
  public username?: string;
  public photo_url?: string;
  public points: number;
  public game_duration: number;
}

export class GetServerTimeDto{
  public data: ServerTimestampDto;
  public message: string;
}

export class ServerTimestampDto{
  public timestamp: number;
}