const { ccclass, property } = cc._decorator;

@ccclass
export default class HistoryItem extends cc.Component {

    @property(cc.Label)
    public label_reward: cc.Label | null = null;

    @property(cc.Label)
    public label_name: cc.Label | null = null;

    @property(cc.Label)
    public label_score: cc.Label | null = null;

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    init(reward: number, name: string,  score: number){
        this.label_reward.string = reward.toFixed(2);
        this.label_name.string = name;
        this.label_score.string = score.toString();
    }
}
