const {ccclass, property} = cc._decorator;

const adsGramLoadPromise = new Promise<any>((resolve, reject) => {
    if (cc.sys.platform === cc.sys.MOBILE_BROWSER || cc.sys.platform === cc.sys.DESKTOP_BROWSER) {
        const script = document.createElement("script");
        script.src = "https://sad.adsgram.ai/js/sad.min.js";
        script.async = true;
        script.onload = () => {
            const intervalId = setInterval(() => {
                if ((window as any).Adsgram) {
                    resolve((window as any).Adsgram);
                    clearInterval(intervalId);
                }
            }, 100);
        };
        script.onerror = () => reject(new Error("Unable to load AdsGram SDK, please check logs."));
        document.head.appendChild(script);
    }
});


@ccclass('AdsGramController')
export class AdsGramController {
    private static _instance: AdsGramController;
    private constructor() {

    }
    public static get Instance(): AdsGramController {
        if (!AdsGramController._instance) {
            AdsGramController._instance = new AdsGramController();
        }
        return AdsGramController._instance;
    }

    private _adsGramJS: any = null;
    private rewarded_adController: AdController = null;
    private interstitial_adController: AdController = null;

    public async init(): Promise<{ success: boolean }> {
        this._adsGramJS = await adsGramLoadPromise;

        if (this._adsGramJS) {
            return Promise.resolve({ success: true });
        } else {
            return Promise.resolve({ success: false });
        }
    }


    public getAdsGram() {
        return this._adsGramJS;
    }

    public initAdsContorllers(){

       // const initParams : AdsgramInitParams = new(){};

        const adsgram = this.getAdsGram();
        const rewarder_adController = adsgram.init({
            blockId: "9539", 
            //debug: true,
            debugBannerType: "FullscreenMedia"
        });
        this.rewarded_adController = rewarder_adController;


        const interstitial_adController = adsgram.init({
           blockId: "int-9540", 
           //debug: true,
           debugBannerType: "FullscreenMedia"
       });

        this.interstitial_adController = interstitial_adController;

    }

    public showAd_Rewarded() : Promise<ShowPromiseResult>{
        return this.rewarded_adController.show();
    }

    public showAd_Intersitital() : Promise<ShowPromiseResult>{
        return this.interstitial_adController.show();
    }

    public showFullscreenAd(){ //rewarded
        this.rewarded_adController.show().then((result: ShowPromiseResult) => {
            // user watch ad till the end or close it in interstitial format
            // your code to reward user for rewarded format

            console.log(`AdsGram ad watched ${result}`)

        }).catch((result: ShowPromiseResult) => {
            // user get error during playing ad or skip ad
            // do nothing or whatever you want

            console.log(`AdsGram ad error: ${result}`)

        })
    }
}


export interface ShowPromiseResult {
    done: boolean;
    description: string;
    state: 'load' | 'render' | 'playing' | 'destroy';
    error: boolean;
  }
  
  type BannerType = 'RewardedVideo' | 'FullscreenMedia';
  
  interface AdsgramInitParams {
    blockId: string;
    debug?: boolean;
    debugBannerType?: BannerType;
  }
  
  type EventType =
    | 'onReward'
    | 'onComplete'
    | 'onStart'
    | 'onSkip'
    | 'onBannerNotFound'
    | 'onNonStopShow'
    | 'onError';
  type HandlerType = () => void;
  
  export interface AdController {
    show(): Promise<ShowPromiseResult>;
    addEventListener(event: EventType, handler: HandlerType): void;
    removeEventListener(event: EventType, handler: HandlerType): void;
    destroy(): void;
  }
  
  declare global {
      interface Window {
        Adsgram?: {
          init(params: AdsgramInitParams): AdController;
        };
      }
  }