//import axios, { AxiosInstance, AxiosRequestConfig } from 'axios';
import axios, { AxiosInstance, AxiosRequestConfig } from "../modules/axios";
import {
    Forbidden,
    HttpError,
    Unauthorized
} from './ApiErrors';
import { Headers } from './ApiTypes';


export class ApiClient {
    constructor(
        private readonly baseUrl: string,
        private readonly headers: Headers,
        private readonly authToken: string = ""
    ) {}

    public async get(endpoint: string = "", params?: any): Promise<any> {
        try {
            const client = this.createClient(params);
            const response = await client.get(endpoint);

             // Ensure the status code is in the success range
             if (response.status >= 200 && response.status < 300) {
                return response.data;
            } else {
                throw new Error(`Unexpected response status: ${response.status} data: ${response.data}`);
            }
        } catch (error: any) {
            this.handleError(error);
        }
    }

    public async post(endpoint: string = "", data?: any): Promise<any> {
        try {
            const client = this.createClient();
            const response = await client.post(endpoint, data);

            console.log("post", response.status, response.data)

            // Ensure the status code is in the success range
            if (response.status >= 200 && response.status < 300) {
                return response.data;
            } else {
                throw new Error(`Unexpected response status: ${response.status} data: ${response.data}`);
            }
        } catch (error) {
            this.handleError(error);
        }
    }

    public async patch(endpoint: string = "", data?: any): Promise<any> {

        try {
            const client = this.createClient();
            const response = await client.patch(endpoint, data);
    
            console.log("patch", response.status, response.data);
    
            // Ensure the status code is in the success range
            if (response.status >= 200 && response.status < 300) {
                return response.data;
            } else {
                throw new Error(`Unexpected response status: ${response.status} data: ${response.data}`);
            }
        } catch (error) {
            this.handleError(error);
        }
    }    

    public async uploadFile(endpoint: string = "", formData: FormData): Promise<any> {
        try {
            const client = this.createClient();
            const response = await client.post(endpoint, formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                }
            })
            return response.data;
        } catch (error) {
            this.handleError(error);
        }
    }

    private createClient(params: object = {}): AxiosInstance {
        const config: AxiosRequestConfig = {
            baseURL: this.baseUrl,
            headers: this.headers,
            params: params
        }
        if (this.authToken) {
            config.headers = {
                Authorization: `tma ${this.authToken}`,
                "ngrok-skip-browser-warning" : "skip-browser-warning",
            }
        }
        return axios.create(config);
    }

    private handleError(error: any): never {
        throw error;
    }
}