 export default class AvatarLoader {
    static loadAvatar(photoUrl: string, spriteNode: cc.Node) {
        if (!photoUrl) {
            console.warn("No photo URL provided.");
            return;
        }

        // Ensure the node has a Sprite component
        const sprite = spriteNode.getComponent(cc.Sprite);
        if (!sprite) {
            console.error("Provided node does not have a Sprite component.");
            return;
        }

        //photoUrl = "https://t.me/i/userpic/320/2uptPMrsaIXViYtEoOWxkkLiDexwDDkQySVmeA8FsxU.svg";

        // Load image from the URL
        cc.assetManager.loadRemote(photoUrl, (err, texture: cc.Texture2D) => {
            if (err) {
                console.error("Failed to load avatar:", err);
                return;
            }

            // Apply the texture to the sprite
            sprite.spriteFrame = new cc.SpriteFrame(texture);
        });
    }
}