export async function getPatchBonusesDto(bomb_new_amount: number, shuffle_new_amount: number, change_block_new_amount: number) : Promise<PatchBonusesDto> {
    const timestamp = getServerTimestampNow();

    const bonuses: BonusesDto = {
        bomb_amount: bomb_new_amount,
        shuffle_amount: shuffle_new_amount,
        change_block_amount: change_block_new_amount,
    }

    var userId;
    try{
        userId = TelegramWebApp.Instance.getTelegramUser().id
    }
    catch{
        await UserController.Instance?.getUser().then((response) => {
            userId = response.data.id;
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });
    }
    
    const config = await ConfigLoader.loadConfig();
    const token = await generateJsonDataToken(userId, timestamp, JSON.stringify(bonuses), config.GAME_SECRET_KEY);

    const data: PatchBonusesDto = {
        bonuses: bonuses,
        token: token,
        timestamp: timestamp
    };

    return data;
}

