import { TaskDto } from "../dtos/user.dto";
import DailyTaskSubItem from "./DailyTaskSubItem";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DailyTaskItem extends cc.Component {

    @property(cc.Sprite)
    public sprite_reward: cc.Sprite | null = null;

    @property(cc.Label)
    public label_reward: cc.Label | null = null;

    @property(DailyTaskSubItem)
    public sub_task_0: DailyTaskSubItem | null = null;

    @property(DailyTaskSubItem)
    public sub_task_1: DailyTaskSubItem | null = null;

    @property(cc.Sprite)
    public fill: cc.Sprite = null;

    @property(cc.SpriteFrame)
    public shuffle_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public bomb_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public replace_frame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public coin_frame: cc.SpriteFrame = null;

    public id: number = null;
    public reward: string = null;
    public amount: number = null;

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    init(dto: TaskDto){
        this.id = dto.id;
        this.reward = dto.reward;
        this.amount = dto.amount;

        this.sprite_reward.spriteFrame = this.getRewardSprite(dto.reward);
        
        this.label_reward.string = `x${dto.amount}`;

        this.sub_task_0.init(dto.sub_tasks[0], dto.id);
        this.sub_task_1.init(dto.sub_tasks[1], dto.id);

        var completed = 0;
        var target = 0;

        var isCompleted = true;

        for (let i = 0; i < dto.sub_tasks.length; i++) {
            isCompleted = isCompleted && (dto.sub_tasks[i].amount >= dto.sub_tasks[i].target_amount);
            completed +=  dto.sub_tasks[i].amount >= dto.sub_tasks[i].target_amount ? dto.sub_tasks[i].target_amount : dto.sub_tasks[i].amount;
            target +=  dto.sub_tasks[i].target_amount;
        }

        this.setCompleted(isCompleted);
        this.setFill(completed/target);
    }

    setFill(fill: number){
        this.fill.fillRange = fill;
    }

    setCompleted(isCompleted: boolean){
        this.node.opacity = isCompleted ? 130 : 255;
    }

    getRewardSprite(reward: string) : cc.SpriteFrame{
        switch(reward){
            case "BOMB":
                return this.bomb_frame;
            case "SHUFFLE":
                return this.shuffle_frame; 
            case "CHANGE_BLOCK":
                return this.replace_frame;
            case "COIN":
            case "STAR":
            case "STARS":

                return this.coin_frame;
            default:
                return null;
        }
    }

}