import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { UserDailyTasksDto } from "../dtos/user.dto";
import PoolManager from "../manager/PoolManager";
import DailyTaskItem from "../ui/DailyTaskItem";
import { UserController } from "../api/controllers/AuthController";
import HourlyChallangeItem from "../ui/HourlyChallangeItem";
import { getRemainingTimeToDateSeconds } from "../Utils";

const { ccclass, property } = cc._decorator;

@ccclass
export default class TasksLayer extends BaseLayer {

    
    info_popup: cc.Node = null

    week_tasks_bar: cc.Node = null
    week_tasks_bar_base: cc.Node = null
    week_tasks_bar_streak: cc.Node = null

    week_tasks_bar_fill: cc.Sprite = null;
    week_tasks_bar_amount: cc.Label = null;
    week_tasks_bar_streak_amount: cc.Label = null;
    
    info_popup_tasks_fill: cc.Sprite = null;
    info_popup_tasks_amount: cc.Label = null;
    info_popup_streak: cc.Node = null;
    info_popup_streak_amount: cc.Label = null;
    info_popup_timeLeft: cc.Label = null;


    dailyPanel: cc.Node = null
    invitePanel: cc.Node = null

    btnCloseInfo: cc.Node = null

    btnDailyTab: cc.Node = null
    btnInviteTab: cc.Node = null
    btnInviteFriend: cc.Node = null

    dailyPanelContent: cc.Node = null

    //bottomPanel
    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null
    btnOutput: cc.Node = null

    onLoad() {

        //bottom
        this.btnMain = cc.find('tabs_panel/btn_main', this.node)
        this.btnLeaderboard = cc.find('tabs_panel/btn_leader', this.node)
        this.btnTasks = cc.find('tabs_panel/btn_tasks', this.node)
        this.btnProfile = cc.find('tabs_panel/btn_profile', this.node)
        this.btnOutput = cc.find('tabs_panel/btn_out', this.node)

        this.btnMain.on('click', this.onMainClick, this)
        this.btnLeaderboard.on('click', this.onLeaderboardClick, this)
        this.btnTasks.on('click', this.onTasksClick, this)
        this.btnProfile.on('click', this.onProfileClick, this)
        this.btnOutput.on('click', this.onOutputClick, this)

        //screens
        this.info_popup = cc.find('info_popup', this.node)
        this.dailyPanel = cc.find('daily', this.node)
        this.invitePanel = cc.find('invite', this.node)

        //content -- tasks
        this.dailyPanelContent = cc.find('main/scroll_view/view/content', this.dailyPanel)

        //week task bar
        this.week_tasks_bar = cc.find('main/tasks_weekly', this.dailyPanel)
        this.week_tasks_bar_base = cc.find('base', this.week_tasks_bar)
        this.week_tasks_bar_streak = cc.find('streak', this.week_tasks_bar)

        this.week_tasks_bar_fill = cc.find('fill_bar/fill', this.week_tasks_bar_base).getComponent(cc.Sprite)
        this.week_tasks_bar_amount = cc.find('amount', this.week_tasks_bar_base).getComponent(cc.Label)
        this.week_tasks_bar_streak_amount = cc.find('layout/label', this.week_tasks_bar_streak).getComponent(cc.Label)

        //info popup
        this.info_popup_tasks_fill = cc.find('panel/games_weekly/fill_bar/fill', this.info_popup).getComponent(cc.Sprite)
        this.info_popup_tasks_amount = cc.find('panel/games_weekly/amount', this.info_popup).getComponent(cc.Label)
        this.info_popup_streak = cc.find('panel/streak', this.info_popup)
        this.info_popup_streak_amount = cc.find('panel/streak/label', this.info_popup).getComponent(cc.Label)
        this.info_popup_timeLeft = cc.find('panel/bottom/time_text', this.info_popup).getComponent(cc.Label)

        this.btnCloseInfo = cc.find('panel/btn_close', this.info_popup)
        this.btnDailyTab = cc.find('btn_tab_daily', this.invitePanel)
        this.btnInviteTab = cc.find('btn_tab_invite', this.dailyPanel)
        this.btnInviteFriend = cc.find('main/task_item_invite/panel/btn_invite', this.invitePanel)

        this.btnDailyTab.on('click', this.onTabDailyClick, this)
        this.btnInviteTab.on('click', this.onTabInviteClick, this)
        this.btnInviteFriend.on('click', this.onInviteFriendClick, this)

        this.week_tasks_bar.on('click', this.openInfoPopupClick, this)
        this.btnCloseInfo.on('click', this.closeInfoPopupClick, this)


        this.setWeeklyAmount(0,14);
        this.setStreakAmount(0);
    }

    onDestroy() {
        this.btnMain.off('click', this.onMainClick, this)
        this.btnLeaderboard.off('click', this.onLeaderboardClick, this)
        this.btnTasks.off('click', this.onTasksClick, this)
        this.btnProfile.off('click', this.onProfileClick, this)
        this.btnOutput.off('click', this.onOutputClick, this)

        this.btnDailyTab.off('click', this.onTabDailyClick, this)
        this.btnInviteTab.off('click', this.onTabInviteClick, this)
        this.btnInviteFriend.off('click', this.onInviteFriendClick, this)

        this.week_tasks_bar.off('click', this.openInfoPopupClick, this)

    }

    async onEnable(){
        while (DataManager.instance.userData == null) {
            await new Promise(resolve => setTimeout(resolve, 10)); // Wait 10ms before checking again
        }

        if(DataManager.instance.userData){
            this.setWeeklyAmount(DataManager.instance.userData.completed_tasks,14);
            this.setStreakAmount(DataManager.instance.userData.weekly_streak);
        }

        if(DataManager.instance.userTasks){
            this.setDailyTasks(DataManager.instance.userTasks, false);
        }

        this.fetchDailyTasks();

       
    }

    protected update(dt: number): void {
        if(DataManager.instance.nextMonday){
            //this.labelGiveawayTimeLeft.string = getRemainingTimeToDateMinutes(DataManager.instance.nextMonday);
            this.info_popup_timeLeft.string = getRemainingTimeToDateSeconds(DataManager.instance.nextMonday);
        } 
    }

    setWeeklyAmount(played : number, target : number){
        const fillRange = played / target;
        this.week_tasks_bar_amount.string = this.info_popup_tasks_amount.string = `${played}/${target}`
        this.week_tasks_bar_fill.fillRange = this.info_popup_tasks_fill.fillRange = fillRange;
    }

    setStreakAmount( days : number){
        this.week_tasks_bar_streak_amount.string = `x${days}`;
        this.info_popup_streak_amount.string = `streak x${days}`;
        this.week_tasks_bar_streak.active = days > 0;
        this.week_tasks_bar_streak.active = days > 0;
    }

    fetchDailyTasks(){
        UserController.Instance?.getUserDailyTasks().then((response) => {
            DataManager.instance.userTasks = response.data;
            this.setDailyTasks(response.data);
        }).catch((error) => {
            console.error("Failed to fetch tasks:", error);
        });
    }

    setDailyTasks(dto: UserDailyTasksDto, fetchHourlyChallange: boolean = true){
        this.dailyPanelContent.destroyAllChildren();
        const poolManager = PoolManager.instance;


        const hourly_challenge_item = PoolManager.instance.getNode(`task_item_hourly_challenge`, this.dailyPanelContent).getComponent(HourlyChallangeItem);
        if(fetchHourlyChallange){
            hourly_challenge_item.fetchFromServer();
        }
        else{
            hourly_challenge_item.setFromCache();
        }

        for (let i = 0; i < dto.tasks.length; i++) {
            const item = poolManager.getNode(`task_item_new`, this.dailyPanelContent).getComponent(DailyTaskItem);
            item.init(dto.tasks[i]);
        }
    }

    onTabDailyClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.dailyPanel.active = true;
        this.invitePanel.active = false;
    }

    onTabInviteClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.dailyPanel.active = false;
        this.invitePanel.active = true;
    }
    
    openInfoPopupClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.info_popup.active = true;
    }

    closeInfoPopupClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.info_popup.active = false;
    }

    onInviteFriendClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        TelegramWebApp.Instance.shareReferral();
    }

    onMainClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onLeaderboardClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTasksClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
    }

    onProfileClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE)
    }

    onOutputClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.OUTPUT)
    }

}
