import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from "../StaticInstance";
import { generateJsonDataToken } from "../Utils";
import { AdsManager } from "../ads/AdsManager";
import { MonetagController } from "../ads/monetag/Monetag";
import { UserController } from "../api/controllers/AuthController";
import { BonusesDto, PatchBonusesDto, PatchTaskDataDto, PatchTaskDto, SubTaskDto } from "../dtos/user.dto";
import TasksLayer from "../layer/TasksLayer";
import AudioManager from "../manager/AudioManager";
import DataManager from "../manager/DataManager";
import LocalizationManager from "../manager/LocalizationManager";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import ConfigLoader from "../utils/ConfigLoader";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DailyTaskSubItem extends cc.Component {

    @property(cc.Label)
    public label_name: cc.Label | null = null;

    @property(cc.Label)
    public label_amount: cc.Label | null = null;

    @property(cc.Button)
    public btn: cc.Button | null = null;

    @property(cc.Node)
    public task_done: cc.Node | null = null;

    @property(cc.Node)
    public task_not_done: cc.Node | null = null;

    public type: string = null;
    public task_id: number = null;
    public done_amount: number = null;

    init(dto: SubTaskDto, task_id: number){
        this.type = dto.type;
        this.task_id = task_id;
        this.label_name.string = LocalizationManager.instance.getLocalizedString(`subtask_${ dto.type}`);
        this.done_amount = dto.amount;

        const showAmount = dto.amount > dto.target_amount ? dto.target_amount : dto.amount;

        this.label_amount.string = `${showAmount}/${dto.target_amount}`;

        this.task_done.active = dto.amount >= dto.target_amount;
        this.btn.interactable = this.task_not_done.active = !(this.task_done.active);
    }

    protected onLoad(): void {
        this.btn?.node.on('click', this.onButtonClicked, this)
    }

    protected onDestroy(): void {
        //this.btn?.node.off('click', this.onButtonClicked, this)
    }

    onButtonClicked(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        switch(this.type){
            case "ads":
                this.showRewardedAd();     
                break;
            case "reaction_to_post":
                //open group and update
                this.openTelegramGroup();
                break;
            case "play_game":
                //start game
                this.startGame();
                break;
            default:
                break;
        }
    }

    async showRewardedAd(){

        const adWatched = await AdsManager.Instance.showAd_Rewarded_Intestitial();

        if (adWatched) {
            //giveRewardToUser
            console.log('Ad successfully watched!');
            this.adWathcedSuccesfully();

        } else {
          console.log("User didn't watch the ad. No reward granted.");
        }

        // //MonetagController.Instance.old_2_showRewarded_Interstitial()
        // AdsManager.Instance.showAd_Rewarded_Intestitial()
        // .then(() => {
        //     // Ad successfully watched, resolve the reward logic here
        //     console.log('Ad successfully watched!');
        //     this.adWathcedSuccesfully();
        // })
        // .catch((error) => {
        //     // Ad failed to show or was skipped, handle rejection
        //     console.error('Ad failed or was skipped:', error);

        //     // MonetagController.Instance.showOncklicka_Rewarded_1()
        //     // .then(() => {
        //     //     // Ad successfully watched, resolve the reward logic here
        //     //     console.log('Ad successfully watched!');
        //     //     this.adWathcedSuccesfully();
        //     // })
        //     // .catch((error) => {
        //     //     // Ad failed to show or was skipped, handle rejection
        //     //     console.error('Ad 2 failed or was skipped:', error);
        //     // });
        // });
    }

    adWathcedSuccesfully(){
        this.incrementTaskAndUpdate();
    }

    openTelegramGroup(){
        TelegramWebApp.Instance.openTelegramLinkChannel();
        this.incrementTaskAndUpdate();
    }

    async startGame(){
        console.log("aaa")
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        DataManager.instance.mode = ENUM_GAME_MODE.SCORE
        await StaticInstance.fadeManager.fadeIn()
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MAIN)
        DataManager.instance.score = 0
        StaticInstance.gameManager.onGameStart()
    }

    async incrementTaskAndUpdate(){
        const data: PatchTaskDto = await this.getPatchTaskDto();
        UserController.Instance.patchDailyTasksIncrement(data).then(()=>{
            //update
            this.updateTasks();
        })
    }

    updateTasks(){
        (StaticInstance.uiManager.getLayerInstance(ENUM_UI_TYPE.TASKS) as TasksLayer).fetchDailyTasks();
    }

    async getPatchTaskDto() : Promise<PatchTaskDto> {
        const timestamp = Date.now();
        const task: PatchTaskDataDto = {
            id: this.task_id,
            type: this.type,
            amount: 1
        }

        var userId;
        try{
            userId = TelegramWebApp.Instance.getTelegramUser().id
        }
        catch{
            await UserController.Instance?.getUser().then((response) => {
                userId = response.data.id;
            }).catch((error) => {
                console.error("Failed to fetch user data:", error);
            });
        }
        
        const config = await ConfigLoader.loadConfig();
        const token = await generateJsonDataToken(userId, timestamp, JSON.stringify(task), config.GAME_SECRET_KEY);

        const data: PatchTaskDto = {
            task: task,
            token: token,
            timestamp: timestamp
        };

        return data;
    }
}