import { Leaderboard } from "../types";
import {ApiClient} from "./ApiClient";
import {TestModel} from "./models/TestModel";

export class TestManager {
    constructor(private readonly apiClient: ApiClient) {}


    public static Instance: TestManager = null


    public async getTest(): Promise<TestModel> {
        return this.apiClient.get("test/123");
    }

    public async postTest(): Promise<TestModel> {
        const data: TestModel = {
            id: 1,
            text: "Hello, world!"
        };
        return this.apiClient.post("test/", data);
    }


    public async getLeaderboard(): Promise<Leaderboard> {
        return this.apiClient.get("user/leaderboard/226496203");
    }
}