import { ENUM_RBLOCK_STATUS } from "../Enum";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BlockReady extends cc.Component {
    @property
    basicPos: cc.Vec2 = cc.v2(0, 0)
    index: number = -1
    pos: cc.Vec2 = null
    size: cc.Size = null
    scale: number = 0.45
    isActive: boolean = false
    currentPos: cc.Vec2 = null
    status: ENUM_RBLOCK_STATUS = ENUM_RBLOCK_STATUS.SHOW

    init(index: number, pos: cc.Vec2, scale: number = 0.6){

        //console.log("INIT BLOCK " + this.node.name + " pos " + pos + " index " + index);

        this.index = index
        this.pos = pos
        this.node.scale = scale
        this.node.setPosition(pos)
        this.setOpacity(ENUM_RBLOCK_STATUS.HIDE)

        this.node.getComponent(cc.Layout).enabled = false;
        this.size = this.node.getContentSize();
        //this.node.setContentSize(this.node.parent.getContentSize())
    }

    setOpacity(status: ENUM_RBLOCK_STATUS) {
        this.status = status
        this.node.opacity = status
    }

    setActive(bool: boolean = true){
        this.isActive = bool
        if(bool){
            const {x, y} = this.node
            this.currentPos = cc.v2(x, y + 150)
            this.node.setPosition(this.currentPos)
            this.node.scale = 1
            this.node.zIndex = 10
            this.node.setContentSize(this.size)
        }else{
            //console.log("BLOCK SET ACTIVE FALSE " + this.node.name + " pos " + this.pos + " index " + this.index);
            this.node.setPosition(this.pos)
            this.node.scale = this.scale
            this.node.zIndex = 1
            this.node.setContentSize(100 / this.scale, 100 / this.scale)
        }
    }

    setMove(x: number, y: number){
        if(this.currentPos){
            const mx = this.currentPos.x + x
            const my = this.currentPos.y + y
            this.currentPos = cc.v2(mx, my)
            this.node.setPosition(this.currentPos)
        }
    }

    getRelPos(node: cc.Node){
        const wpos = this.node.convertToWorldSpaceAR(this.basicPos)
        const rpos = node.convertToNodeSpaceAR(wpos)
        return rpos
    }
}
