
import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE, ENUM_GAME_STATUS} from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import { UserController } from "../api/controllers/AuthController";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";

const { ccclass, property } = cc._decorator;

@ccclass
export default class HttpErrorLayer extends BaseLayer {

    panel: cc.Node = null
    btnSupport: cc.Node = null
    btnRefresh: cc.Node = null
   
    tip: cc.Label = null
    title: cc.Label = null
    errorMessage: cc.Label = null

    init(message: string){
        //todo. pass specific error and refresh callback action
        this.errorMessage.string = message;
    }

    onLoad() {
        this.panel = cc.find('panel', this.node)
        this.btnSupport = cc.find('buttons/btn_support', this.panel)
        this.btnRefresh = cc.find('buttons/btn_refresh', this.panel)
        
        this.tip = cc.find('tip', this.panel).getComponent(cc.Label)
        this.title = cc.find('title', this.panel).getComponent(cc.Label)
        this.errorMessage = cc.find('errorMessage', this.panel).getComponent(cc.Label)

        this.btnSupport.on('click', this.onSupportClick, this)
        this.btnRefresh.on('click', this.onRefreshClick, this)

    }

    onDestroy() {
        this.btnSupport.off('click', this.onSupportClick, this)
        this.btnRefresh.off('click', this.onRefreshClick, this)
    }

    onSupportClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        //todo open support page
        TelegramWebApp.Instance?.openTelegramLinkSupport();
    }

    onRefreshClick() {
        
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        UserController.Instance.getUser().then(function (response) {
            //on success
            DataManager.instance.userData = response.data;
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HTTP_ERROR, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
        }).catch(error =>{
            // Операції з UI
            this.init(error.message);
            console.log(JSON.stringify(error))
        });
    }

    onHomeClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        DataManager.instance.setGameStatusUnrunning();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MAIN, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.EXIT, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LOSE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }
}
