export class StartGameDto {
  game_token!: string;
  timestamp!: string;
}

export class FinishGameDto {
  points!: number;
  game_token!: string;
  timestamp!: string;
}

export interface GameStartResponceDto {
  id: number;                   // Unique game identifier
  user_id: number;              // User ID associated with the game
  points: number;               // Initial points, usually 0 at game start
  ended_at: string | null;      // Null, as the game hasn't ended yet
  created_at: string;           // Timestamp when the game was created
  //game_token: string;           // Unique token for the game session
}

export interface GameFinishResponceDto {
  id: number;                   // Unique game identifier
  user_id: number;              // User ID associated with the game
  points: number;               // Final points earned by the user
  ended_at: string;             // Timestamp when the game ended
  created_at: string;           // Timestamp when the game was created
}

