// Created by carolsail

import { ENUM_AUDIO_CLIP, ENUM_GAME_STATUS, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import SdkManager from "../manager/SdkManager";
import LocalizationManager from "../manager/LocalizationManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SettingLayer extends BaseLayer {

    btnClose: cc.Node = null

    btnMusic: cc.Node = null
    btnSound: cc.Node = null

    btnLangUa: cc.Node = null
    btnLangEn: cc.Node = null
    btnLangDe: cc.Node = null
    btnLangEs: cc.Node = null
    btnLangRu: cc.Node = null

    //bottomPanel
    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null
    btnOutput: cc.Node = null

    //toggleGroup: cc.ToggleGroup = null
    toggleContainer: cc.ToggleContainer = null

    togglesNode: cc.Node = null


    toggleEN_node: cc.Node = null
    toggleUK_node: cc.Node = null
    toggleDE_node: cc.Node = null
    toggleES_node: cc.Node = null
    toggleRU_node: cc.Node = null
    toggleCS_node: cc.Node = null //Czech  cs
    toggleSK_node: cc.Node = null //Slovak sk
    togglePL_node: cc.Node = null //Polish pl


    toggleEN: cc.Toggle = null
    toggleUK: cc.Toggle = null
    toggleDE: cc.Toggle = null
    toggleES: cc.Toggle = null
    toggleRU: cc.Toggle = null
    toggleCS: cc.Toggle = null
    toggleSK: cc.Toggle = null
    togglePL: cc.Toggle = null


    onLoad() {
        this.togglesNode = cc.find('main/lang_selection/items', this.node);
        this.toggleContainer =  this.togglesNode.getComponent(cc.ToggleContainer);

        this.toggleUK_node = cc.find('toggle_lang_ua',  this.togglesNode);
        this.toggleEN_node = cc.find('toggle_lang_en',  this.togglesNode);
        this.toggleDE_node = cc.find('toggle_lang_de',  this.togglesNode);
        this.toggleES_node = cc.find('toggle_lang_es',  this.togglesNode);
        this.toggleRU_node = cc.find('toggle_lang_ru',  this.togglesNode);
        this.toggleCS_node = cc.find('toggle_lang_cs',  this.togglesNode);
        this.toggleSK_node = cc.find('toggle_lang_sk',  this.togglesNode);
        this.togglePL_node = cc.find('toggle_lang_pl',  this.togglesNode);


        console.log(this.toggleUK_node != null)
        console.log(this.toggleEN_node != null)
        console.log(this.toggleDE_node != null)
        console.log(this.toggleES_node != null)
        console.log(this.toggleRU_node != null)
        console.log(this.toggleCS_node != null)
        console.log(this.toggleSK_node != null)
        console.log(this.togglePL_node != null)

        this.toggleUK = this.toggleUK_node.getComponent(cc.Toggle);
        this.toggleEN = this.toggleEN_node.getComponent(cc.Toggle);
        this.toggleDE = this.toggleDE_node.getComponent(cc.Toggle);
        this.toggleES = this.toggleES_node.getComponent(cc.Toggle);
        this.toggleRU = this.toggleRU_node.getComponent(cc.Toggle);
        this.toggleCS = this.toggleCS_node.getComponent(cc.Toggle);
        this.toggleSK = this.toggleSK_node.getComponent(cc.Toggle);
        this.togglePL = this.togglePL_node.getComponent(cc.Toggle);

        console.log(this.toggleUK != null)
        console.log(this.toggleEN != null)
        console.log(this.toggleDE != null)
        console.log(this.toggleES != null)
        console.log(this.toggleRU != null)
        console.log(this.toggleCS != null)
        console.log(this.toggleSK != null)
        console.log(this.togglePL != null)

        this.setupListeners();

        this.btnMusic = cc.find('main/btns_group_1/btn_music', this.node)
        this.btnSound = cc.find('main/btns_group_1/btn_sound', this.node)
        this.btnClose = cc.find('btn_close', this.node)

        this.btnMain = cc.find('tabs_panel/btn_main', this.node)
        this.btnLeaderboard = cc.find('tabs_panel/btn_leader', this.node)
        this.btnTasks = cc.find('tabs_panel/btn_tasks', this.node)
        this.btnProfile = cc.find('tabs_panel/btn_profile', this.node)
        this.btnOutput = cc.find('tabs_panel/btn_out', this.node)


        this.btnMusic.on('click', this.onMusicClick, this)
        this.btnSound.on('click', this.onSoundClick, this)
        this.btnClose.on('click', this.onCloseClick, this)

        this.btnMain.on('click', this.onMainClick, this)
        this.btnLeaderboard.on('click', this.onLeaderboardClick, this)
        this.btnTasks.on('click', this.onTasksClick, this)
        this.btnProfile.on('click', this.onProfileClick, this)
        this.btnOutput.on('click', this.onOutputClick, this)

        this.renderCurrentLanguage();

    }

    onDestroy() {
        this.btnMusic.off('click', this.onMusicClick, this)
        this.btnSound.off('click', this.onSoundClick, this)
        this.btnClose.off('click', this.onCloseClick, this)
        
        this.btnMain.off('click', this.onMainClick, this)
        this.btnLeaderboard.off('click', this.onLeaderboardClick, this)
        this.btnTasks.off('click', this.onTasksClick, this)
        this.btnProfile.off('click', this.onProfileClick, this)
        this.btnOutput.off('click', this.onOutputClick, this)
    }

    onEnable() {
        //this.zoomIn(this.panel)
        this.rendorMusic()
        this.rendorSound()
        SdkManager.instance.showInterstitialAd()
    }

    onDisable() { }

    onCloseClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onSoundClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        DataManager.instance.isSoundOn = !DataManager.instance.isSoundOn
        DataManager.instance.save()
        this.rendorSound()
    }

    onMusicClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        DataManager.instance.isMusicOn = !DataManager.instance.isMusicOn
        DataManager.instance.save()
        if (DataManager.instance.isMusicOn) {
            AudioManager.instance.playMusic()
        } else {
            AudioManager.instance.stopMusic()
        }
        this.rendorMusic()
    }

    onMainClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onLeaderboardClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTasksClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
    }

    onProfileClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING)
    }

    onOutputClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.OUTPUT)
    }

    rendorMusic() {
        if(!this.btnMusic)
            return;

        this.btnMusic.getChildByName('on').active = DataManager.instance.isMusicOn
        this.btnMusic.getChildByName('off').active = !DataManager.instance.isMusicOn
    }

    rendorSound() {
        if(!this.btnSound)
            return;
        this.btnSound.getChildByName('on').active = DataManager.instance.isSoundOn
        this.btnSound.getChildByName('off').active = !DataManager.instance.isSoundOn
    }

    renderCurrentLanguage(){

        this.toggleEN.isChecked = DataManager.instance.language == "en";
        this.toggleUK.isChecked =  DataManager.instance.language == "uk" || DataManager.instance.language == "uk";
        this.toggleDE.isChecked =  DataManager.instance.language == "de";
        this.toggleES.isChecked =  DataManager.instance.language == "es";
        this.toggleRU.isChecked =  DataManager.instance.language == "ru";
        this.toggleCS.isChecked =  DataManager.instance.language == "cs";
        this.toggleSK.isChecked =  DataManager.instance.language == "sk";
        this.togglePL.isChecked =  DataManager.instance.language == "pl";
    }

    setupListeners() {
        this.toggleEN_node.on('toggle', () => LocalizationManager.instance.changeLanguage('en'));
        this.toggleUK_node.on('toggle', () => LocalizationManager.instance.changeLanguage('uk'));
        this.toggleDE_node.on('toggle', () => LocalizationManager.instance.changeLanguage('de'));
        this.toggleES_node.on('toggle', () => LocalizationManager.instance.changeLanguage('es'));
        this.toggleRU_node.on('toggle', () => LocalizationManager.instance.changeLanguage('ru'));
        this.toggleCS_node.on('toggle', () => LocalizationManager.instance.changeLanguage('cs'));
        this.toggleSK_node.on('toggle', () => LocalizationManager.instance.changeLanguage('sk'));
        this.togglePL_node.on('toggle', () => LocalizationManager.instance.changeLanguage('pl'));
    }
}
