const {ccclass, property} = cc._decorator;

@ccclass('Richads')
export class Richads {
    private static _instance: Richads;
    private constructor() {

    }

    public static get Instance(): Richads {
        if (!Richads._instance) {
            Richads._instance = new Richads();
        }
        return Richads._instance;
    }

    public init(){
        
    }

    public showAd_Rewarded() : Promise<any>{
        return (window as any).TelegramAdsController.triggerNativeNotification(false);  
    }
}