import * as config from '../../config.js';

const {ccclass, property} = cc._decorator;

const tgLoadPromise = new Promise<any>((resolve, reject) => {
    if (cc.sys.platform === cc.sys.MOBILE_BROWSER || cc.sys.platform === cc.sys.DESKTOP_BROWSER) {
        const script = document.createElement("script");
        script.src = "https://telegram.org/js/telegram-web-app.js";
        script.async = true;
        script.onload = () => {
            const intervalId = setInterval(() => {
                if ((window as any).Telegram && (window as any).Telegram.WebApp) {
                    resolve((window as any).Telegram.WebApp);
                    clearInterval(intervalId);
                }
            }, 100);
        };
        script.onerror = () => reject(new Error("Unable to load TelegramWebApp SDK, please check logs."));
        document.head.appendChild(script);
    }
});

export interface WebAppUser {
    id: number;
    is_bot: boolean;
    first_name: string;
    last_name?: string;
    username?: string;
    language_code?: string;
    is_premium?: boolean;
    added_to_attachment_menu?: boolean;
    allows_write_to_pm?: boolean;
    photo_url?: string;
}
export interface WebAppChat {
    id: number;
    type: 'private' | 'group' | 'supergroup' | 'channel';
    title: string;
    username?: string;
    photo_url?: string;
}
export interface WebAppInitData {
    query_id?: string;
    user?: WebAppUser;
    receiver?: WebAppUser;
    chat?: WebAppChat;
    chat_type?: 'private' | 'group' | 'supergroup' | 'channel';
    chat_instance?: string;
    start_param?: string;
    can_send_after?: number;
    auth_data: number;
    hash: string;
}

@ccclass('TelegramWebApp')
export class TelegramWebApp {
    private static _instance: TelegramWebApp;
    private constructor() {

    }
    public static get Instance(): TelegramWebApp {
        if (!TelegramWebApp._instance) {
            TelegramWebApp._instance = new TelegramWebApp();
        }
        return TelegramWebApp._instance;
    }

    private _tgWebAppJS: any = null;
    public async init(): Promise<{ success: boolean }> {
        this._tgWebAppJS = await tgLoadPromise;

        if (this._tgWebAppJS) {
            return Promise.resolve({ success: true });
        } else {
            return Promise.resolve({ success: false });
        }
    }

    public openTelegramLinkChannel(){
        //this.openTelegramLink("https://t.me/+8brtpInhNUNiNTAy")
        this.openTelegramLink(config.CHANNEL_INVITE_URL)
    }

    public openTelegramLinkSupport(){
        this.openTelegramLink(config.SUPPORT_URL)
    }

    public openLinkPrivacy(){
        this.openLink("https://telegram.org/privacy-tpa")
    }

    public openLinkPartnership(){
        this.openLink("https://docs.google.com/forms/d/e/1FAIpQLScMtlZD_75-r4d1hrkiAh3FaPy2qTYUAV59HvbO4cGBAU4x3A/viewform?usp=dialog")
    }

    public openLinkGuidebook(){
        this.openLink("https://telegram.org")
    }


    public openLink(url: string) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }
        this._tgWebAppJS.openLink(url);
    }

    public openTelegramLink(url: string) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }
        this._tgWebAppJS.openTelegramLink(url);
    }

    public share(url: string, text?: string) {
        const shareUrl = 'https://t.me/share/url?url=' + encodeURIComponent(url) + '&text=' + encodeURIComponent(text || '');
        this.openTelegramLink(shareUrl);
    }

    public shareToStory(media_url: string, shareText?: string, widget_link_url?: string, widget_link_name?: string) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }
        const widget_link = {
            text: shareText,
            widget_link: {
                url: widget_link_url,
                name: widget_link_name,
            },
        };
        this._tgWebAppJS.shareToStory(media_url, widget_link);
    }


    public shareReferral(){
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return;
        }

        const user = this.getTelegramUser();
        const url = `${config.REFERRAL_URL}?startapp=${user.id}`;
        const text = '🔗 Try to beat my top score!';
        this.share(url, text);
    }

    //This method can directly return the TelegramWebApp object, making it convenient to use other capabilities. 
    //For specific methods, please refer to the official documentation: 
    //https://core.telegram.org/bots/webapps#initializing-mini-apps
    public getTelegramWebApp() {
        return this._tgWebAppJS;
    }

    public getTelegramWebAppInitData(): WebAppInitData {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initDataUnsafe;
    }


    public getTelegramUser(): WebAppUser {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initDataUnsafe.user;
    }

    public getTelegramInitData(): string {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        return this._tgWebAppJS.initData;
    }

    public openInvoice(url: string, callback: any) {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        this._tgWebAppJS.openInvoice(url, callback);
    }

    public requestFullscreen() {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }

        try{
            this._tgWebAppJS.requestFullscreen();     
        }
        catch{
            
        }
    }


    public enableClosingConfirmation() {
        if (!this._tgWebAppJS) {
            console.error("telegram web app is not inited!");
            return null;
        }
        
        
        try{
            this._tgWebAppJS.enableClosingConfirmation();     
        }
        catch{
            
        }
    }

    public alert(message: string) {
        this._tgWebAppJS.showAlert(message);
    }

    public hapticLightImpact() {
        this._tgWebAppJS.HapticFeedback.impactOccurred("light") 
    }

    public hapticMediumImpact() {
        this._tgWebAppJS.HapticFeedback.impactOccurred("medium")
    }

    public hapticHeavyImpact() {
        this._tgWebAppJS.HapticFeedback.impactOccurred("heavy")
    }

    public hapticSuccesNotification() {
        this._tgWebAppJS.HapticFeedback.notificationOccurred("success")
    }

    public hapticErrorNotification() {
        this._tgWebAppJS.HapticFeedback.notificationOccurred("error")
    }
}


