import DataManager from "../manager/DataManager";
import LocalizationManager from "../manager/LocalizationManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ThemeSetter extends cc.Component {

    //buttons
    @property(cc.Button)
    public btn_theme_switch: cc.Button = null;

    //nodes
    
    @property(cc.Node)
    public node_theme_dark: cc.Node = null;

    @property(cc.Node)
    public node_theme_light: cc.Node = null;

    @property(cc.Node)
    public node_score_max_icon: cc.Node = null;

    //labels
    @property(cc.Label)
    public label_coins: cc.Label = null;

    @property(cc.Label)
    public label_score_current: cc.Label = null;

    @property(cc.Label)
    public label_score_max: cc.Label = null;

    @property(cc.Label)
    public label_score_max_text: cc.Label = null;

    //sprites

    @property(cc.Sprite)
    public sprite_background: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_coins: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_theme_switch: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_board: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_slot_ready_1: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_slot_ready_2: cc.Sprite = null;

    @property(cc.Sprite)
    public sprite_slot_ready_3: cc.Sprite = null;

    //textures
    @property(cc.SpriteFrame)
    public frame_panel_coin: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public frame_panel_coin_dark: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public frame_board: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public frame_board_dark: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public frame_ready_item: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    public frame_ready_item_dark: cc.SpriteFrame = null;

    onLoad () {
        this.btn_theme_switch.node.on('click', this.switchCurrentTheme, this);
    }

    onDestroy(): void {
        this.btn_theme_switch.node.off('click', this.switchCurrentTheme, this); 
    }

    onEnable(): void {
        this.setTheme(DataManager.instance.isDarkThemeOn);
    }

    switchCurrentTheme(){
        console.log("switchCurrentTheme ")
        
        this.setTheme(!DataManager.instance.isDarkThemeOn);
    }

    setTheme(isDarkThemeOn : boolean){
        console.log("SET THEME ", isDarkThemeOn)
        this.setLabels(isDarkThemeOn);
        this.setSprites(isDarkThemeOn);
        this.setBackground(isDarkThemeOn);

        this.node_theme_dark.active = isDarkThemeOn;
        this.node_theme_light.active = !isDarkThemeOn;

        DataManager.instance.isDarkThemeOn = isDarkThemeOn;
    }

    setBackground(isDarkThemeOn : boolean){
        const darkThemeColor = cc.color(58, 81, 149);
        const lightThemeColor = cc.color(250, 251, 255);

        this.sprite_background.node.color =  isDarkThemeOn ? darkThemeColor : lightThemeColor;
    }

    setLabels(isDarkThemeOn : boolean){
        const lightTheme_darkBlueText = cc.color(34, 47, 63);
        const lightTheme_darkGrayText = cc.color(124, 133, 135);
        const darkTheme_grayText = cc.color(193, 208, 230);

        this.label_coins.node.color = isDarkThemeOn ? darkTheme_grayText : lightTheme_darkBlueText;
        this.label_score_current.node.color = isDarkThemeOn ? darkTheme_grayText : lightTheme_darkBlueText;

        this.label_score_max.node.color = isDarkThemeOn ? darkTheme_grayText : lightTheme_darkGrayText;
        this.label_score_max_text.node.color = isDarkThemeOn ? darkTheme_grayText : lightTheme_darkGrayText;
        this.node_score_max_icon.color = isDarkThemeOn ? darkTheme_grayText : lightTheme_darkGrayText;
    }

    setSprites(isDarkThemeOn : boolean){
        this.sprite_board.spriteFrame = isDarkThemeOn ? this.frame_board_dark : this.frame_board;

        this.sprite_slot_ready_1.spriteFrame = 
        this.sprite_slot_ready_2.spriteFrame = 
        this.sprite_slot_ready_3.spriteFrame = 
            isDarkThemeOn ? this.frame_ready_item_dark : this.frame_ready_item;

        this.sprite_coins.spriteFrame = 
        this.sprite_theme_switch.spriteFrame = 
            isDarkThemeOn ? this.frame_panel_coin_dark : this.frame_panel_coin;
    }
}

