const { ccclass, property } = cc._decorator;
const TEST_TELEGRAM_TOKEN = "user=%7B%22id%22%3A226496203%2C%22first_name%22%3A%22%D1%80%D0%B5%D0%BF%D0%BB%D1%96%D0%BA%D0%B0%D0%BD%D1%82%22%2C%22last_name%22%3A%22%22%2C%22username%22%3A%22pertusini%22%2C%22language_code%22%3A%22uk%22%2C%22allows_write_to_pm%22%3Atrue%2C%22photo_url%22%3A%22https%3A%5C%2F%5C%2Ft.me%5C%2Fi%5C%2Fuserpic%5C%2F320%5C%2F2uptPMrsaIXViYtEoOWxkkLiDexwDDkQySVmeA8FsxU.svg%22%7D&chat_instance=3591124487722041371&chat_type=sender&auth_date=1736760962&signature=Apzb5N74Rrtv7PnYEOs6mKRkStMSBA1La5F5O2lW2w6sYn6SSrIsJj-g3uhMnrEZlHKQGdfrHDEYOVat6KsqCw&hash=d758e391a5af042859b8a90e87a91b0d6be3739c1794f30c943f50d3b468315b";
//const TEST_TELEGRAM_TOKEN ="query_id=AAHLDoANAAAAAMsOgA2RQIty&user=%7B%22id%22%3A226496203%2C%22first_name%22%3A%22%D1%80%D0%B5%D0%BF%D0%BB%D1%96%D0%BA%D0%B0%D0%BD%D1%82%22%2C%22last_name%22%3A%22%22%2C%22username%22%3A%22pertusini%22%2C%22language_code%22%3A%22uk%22%2C%22allows_write_to_pm%22%3Atrue%2C%22photo_url%22%3A%22https%3A%5C%2F%5C%2Ft.me%5C%2Fi%5C%2Fuserpic%5C%2F320%5C%2F2uptPMrsaIXViYtEoOWxkkLiDexwDDkQySVmeA8FsxU.svg%22%7D&auth_date=1741260439&signature=8izuE3Zh-7qtC7jR7pcNwXPIApB_uVc3IOManQjm1FJAzesuCKUWIJ6dM3r3bOXkSNxx0HF3hgf7mSxboT5lBQ&hash=748a2a7f3d8c8b6e2e4e9325b1f25ee7d181b24156772a762b08c946f02f074a";
//const TEST_TELEGRAM_TOKEN = "query_id=AAHLDoANAAAAAMsOgA1v1jqm&user=%7B%22id%22%3A226496203%2C%22first_name%22%3A%22%D1%80%D0%B5%D0%BF%D0%BB%D1%96%D0%BA%D0%B0%D0%BD%D1%82%22%2C%22last_name%22%3A%22%22%2C%22username%22%3A%22pertusini%22%2C%22language_code%22%3A%22uk%22%2C%22allows_write_to_pm%22%3Atrue%2C%22photo_url%22%3A%22https%3A%5C%2F%5C%2Ft.me%5C%2Fi%5C%2Fuserpic%5C%2F320%5C%2F2uptPMrsaIXViYtEoOWxkkLiDexwDDkQySVmeA8FsxU.svg%22%7D&auth_date=1738596311&signature=Be68ytJKSA4LpkaNOt_4kp9UST34QzCy4S6rKuRCASBd8M2h0XEfxn_Apx-XOKNKcXMDJeka-YStZ44TLl2RBA&hash=cfe451b73670d7aa9bf3d66613640b9d10c60453eab20b8479bfce5602e7f204"
@ccclass
export default class Index extends cc.Component {


    onLoad() {
        //console.log(`AA___5555`)
        //console.log(`default DESIGN SIZE ${cc.view.getDesignResolutionSize()}`)
        //console.log(`default VIEW SIZE ${cc.view.getFrameSize()}`)
        //console.log(`default RES POLICY ${cc.view.getResolutionPolicy()}`)

        this.node.getChildByName('UI').opacity = 255
        DataManager.instance.loadingRate = 0

        TelegramWebApp.Instance.init().then(res => {
            TelegramWebApp.Instance.requestFullscreen();
            TelegramWebApp.Instance.enableClosingConfirmation();

            const webApp = TelegramWebApp.Instance.getTelegramWebApp();

            webApp.onEvent('fullscreenChanged', () => {
                console.log('fullscreenChanged')
            }) 

            webApp.onEvent('viewportChanged', () => {
                console.log(`viewportChanged.`)
                setDesignResolution();
                this.responsive();
            })
		});

        cc.view.setResizeCallback(() => this.responsive())

        AdsManager.Instance.init();


        //Заборонити прокрутку сторінки під час фокусу на EditBox
        document.body.addEventListener('touchmove', function (e) {
            if (document.activeElement.tagName === 'INPUT' || document.activeElement.tagName === 'TEXTAREA') {
              e.preventDefault();
            }
          }, { passive: false });
    }

    async start() {
        const configRes = await ConfigLoader.loadConfig();

        // Завантаження ресурсів
        for (const index in ENUM_RESOURCE_TYPE) {
            const resource = ENUM_RESOURCE_TYPE[index]
            await ResourceManager.instance.loadRes(resource)
        }

        // Завантаження UI
        StaticInstance.uiManager.init()
        // Відновлення даних
        DataManager.instance.init(configRes);
        DataManager.instance.restore()
        // Відтворення музики
        AudioManager.instance.playMusic()
        // Ініціалізація SDK
        SdkManager.instance.initAudioEndListener()
        SdkManager.instance.passiveShare()
        SdkManager.instance.getRank()
        SdkManager.instance.initBannerAd()
        SdkManager.instance.initInterstitialAd()
        SdkManager.instance.initVideoAd()
        SdkManager.instance.initCustomRowAd()
        SdkManager.instance.initCustomColAd()
        
        DataManager.instance.loadingRate = 1

        setDesignResolution();


        //ініціалізація API клієнта
        await this.initApiClient();

        try {
            const response = await UserController.Instance.getUser();
            // handle success
            console.log("user success");
            DataManager.instance.userData = response.data;
        
            if (response.data.first_login) {
                LocalizationManager.instance.changeLanguage(response.data.language_code);
            }

            //time 

            const timeResponce = await UserController.Instance.getTime();
            console.log("time success", timeResponce.data.timestamp);

            const serverDate = new Date(timeResponce.data.timestamp); 
            const localDate = new Date();
            const timeDiffMiliseconds = serverDate.getTime() - localDate.getTime(); // різниця в мілісекундах

            console.log("timeDiffMiliseconds", timeDiffMiliseconds, getServerTimeNow());

            DataManager.instance.serverTimestamp = timeResponce.data.timestamp; 
            DataManager.instance.serverTimeOffsetMiliseconds = timeDiffMiliseconds;

            // UI operations
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, true);
        } catch (error) {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, true);
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HTTP_ERROR, true, () => {
                (StaticInstance.uiManager.getLayerInstance(ENUM_UI_TYPE.HTTP_ERROR) as HttpErrorLayer).init(error.message);
                console.log(JSON.stringify(error));
            });
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false);
            return;
        }


        UserController.Instance?.getUserDailyTasks().then((response) => {
            DataManager.instance.userTasks = response.data;
        }).catch((error) => {
            console.error("Failed to fetch tasks:", error);
        });

        //patchBonusesNewAmount(5, 5, 5)
    }

    async initApiClient() : Promise<any>{
        const apiClientFactory = new ApiClientFactory(config.API_URL, {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "ngrok-skip-browser-warning" : "skip-browser-warning",
        });

        var token = null;
        var tries = 0;
        do{
            try{
                tries++;
                //token = TelegramWebApp.Instance.getTelegramInitData();
                token = TEST_TELEGRAM_TOKEN;
                const apiClientAuthorized = apiClientFactory.createAuthorizedClient(token)
                const userController = new UserController(apiClientAuthorized);
                UserController.Instance = userController;
            }
            catch{
                console.log("failed getTelegramInitData. retrying..")
                await this.delay(500); // Waits for 0.5 seconds
            }
        }
        while(!token && tries < 5)
    }

    delay(ms: number): Promise<void> {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    // Адаптивність екрану
    responsiveOld() {
        console.log(`RESPONSIVE OLD`);

        const designSize = cc.view.getDesignResolutionSize();
        const viewSize = cc.view.getFrameSize();

        console.log(`RESPONSIVE OLD DESIGN SIZE ${designSize}`)
        console.log(`RESPONSIVE OLD VIEW SIZE ${viewSize}`)

        const setFitWidth = () => {
            console.log("setFitWidth")
            cc.Canvas.instance.fitHeight = false;
            cc.Canvas.instance.fitWidth = true;
        }

        const setFitHeight = () => {
            console.log("setFitHeight")
            cc.Canvas.instance.fitHeight = true;
            cc.Canvas.instance.fitWidth = false;
        }

        const setFitBoth = () => {
            console.log("setFitHeight")
            cc.Canvas.instance.fitHeight = true;
            cc.Canvas.instance.fitWidth = true;
        }

        const designRatio = designSize.width / designSize.height
        const viewRatio = viewSize.width / viewSize.height
        if (designRatio < 1) {
            // console.error('--Гра у вертикальному режимі')
            if (viewRatio < 1) {
                if (viewRatio > designRatio) {
                    setFitBoth()
                } else {
                    setFitWidth()
                }
            } else {
                setFitBoth()
            }
        } else {
            // console.error('--Гра у горизонтальному режимі')
            if (viewRatio > 1) {
                if (viewRatio < designRatio) {
                    setFitBoth()
                } else {
                    setFitHeight()
                }
            } else {
                setFitBoth()
            }
        }
    }


    // Адаптивність екрану
    responsive() {
        if(OutputLayer.block_resize){
            return;
        }

        this.responsiveOld();
    }

}
