import { ENUM_UI_TYPE } from "../Enum";
import DataManager from "../manager/DataManager";
import BaseLayer from "./Baselayer";
import { StaticInstance } from "../StaticInstance";

const {ccclass, property} = cc._decorator;

@ccclass
export default class LoadingLayer extends BaseLayer {

    @property(cc.Label)
    label: cc.Label = null

    @property(cc.Sprite)
    loadfill: cc.Sprite = null

    @property(cc.Node)
    progress: cc.Node = null

    @property(cc.Node)
    connect: cc.Node = null

    @property(cc.Node)
    loadingImage: cc.Node = null

    start(){

        cc.tween(this.loadingImage)
        .by(3, { angle: -360 }) // Rotate 360 degrees in 1 second
        .repeatForever() // Repeat indefinitely
        .start();
    }

    update(dt: number) {
        if(this.loadfill && this.node.active) {
            this.loadfill.fillRange = DataManager.instance.loadingRate
            if(DataManager.instance.loadingRate >= 1){
                // menu已加载完毕
                if(StaticInstance.uiManager.isActive(ENUM_UI_TYPE.MENU) && DataManager.instance.userData != null){
                    this.hide()
                }
                else{
                    this.progress.active = false;
                    this.connect.active = true;
                }
            }
            // if(DataManager.instance.loadingMessage != null){
            //     this.setString(DataManager.instance.loadingMessage)
            // }
        }
    }

    setString(text: string){
        if(this.label){
            this.label.string = text;
        }
    }
}
