const { ccclass, property } = cc._decorator;

@ccclass
export default class SizeFitter extends cc.Component {

    @property({
        type: cc.Boolean,
        displayName: "Fit Widht",
    })
    fitWidht: boolean = true;

    @property({
        type: cc.Boolean,
        displayName: "Fit Height",
    })
    fitHeight: boolean = true;

    onLoad() {
        cc.view.setResizeCallback(() => this.fit())
        this.fit();
    }

    fit(){
         // Get the design resolution and screen resolution
        const canvas = this.node.getComponent(cc.Canvas);
        const designResolution = cc.view.getDesignResolutionSize();
        const frameSize = cc.view.getFrameSize();

        // Calculate aspect ratios
        const designAspectRatio = designResolution.width / designResolution.height;
        const frameAspectRatio = frameSize.width / frameSize.height;

        // Check if the frame aspect ratio is wider
        if (frameAspectRatio > designAspectRatio) {
            // Update canvas width to fit the frame's aspect ratio
            canvas.fitWidth = true;
            canvas.fitHeight = false;

            const newWidth = designResolution.height * frameAspectRatio;
            canvas.designResolution = cc.size(newWidth, designResolution.height);
        } else {
            // Otherwise, fit height normally
            canvas.fitWidth = false;
            canvas.fitHeight = true;
        }
    }

}