import LocalizationManager from "../manager/LocalizationManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ALocaleLabel extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property
    stringLocalizationKey: string = 'testKey';

    onLoad () {
        this.fetchRender();
    }

    onEnable(): void {
        this.fetchRender();
    }

    fetchRender () {
        let label = this.getComponent(cc.Label);
        if (label) {
            this.label = label;
            this.updateLabel();
            return;
        } 
    }

    updateLabel () {
        if (!this.label) {
            cc.error('Failed to update localized label, label component is invalid!');
            return;
        }
        let localizedString = LocalizationManager.instance.getLocalizedString(this.stringLocalizationKey);
        if (localizedString) {
            this.label.string = localizedString;
        }
    }
}

